#app {
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
}

.app-screen {
	display: flex;
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	//overflow: auto;

	@include fix-left-next-to-navbar;

	> main {
		height: calc(100% - $top-app-bar-height);
		margin-top: $top-app-bar-height;
		flex-basis: 0;
		flex-grow: 1;

		// Add padding so the topmost content isn't covered by the toolbar:
		//padding-top: $top-app-bar-height;
		position: relative;
		// Add extra padding at the bottom on the iPhone X
		padding-bottom: constant(safe-area-inset-bottom);
		padding-bottom: env(safe-area-inset-bottom);

		overflow: auto;
	}
}
.top-app-bar {
	height: $top-app-bar-height;
	box-shadow: $box-shadow;
	position: fixed;
	top: 0;
	right: 0;

	@include fix-left-next-to-navbar;

	z-index: $zindex-fixed;
	margin-left: 0;
	margin-right: 0;
	//transition: left 0.1s ease-in;

	background-color: var(--theme-primary);
	color: var(--theme-primary-text);

	.top-app-bar-text {
		text-align: center;
	}
	.btn-icon.btn-primary {
		background-color: transparent;
		border-color: transparent;
		&:hover,
		&:active {
			background-color: rgba(0, 0, 0, 0.2);
			border-color: white;
		}
	}
}

.skip-to-content-link {
    position: fixed;
    top: -1000px;
    background-color: white;
    opacity: 0;
    z-index: 1;

    &:focus {
		top: 1em;
		left:2em;
        opacity: 1;
    }

}
