*,
*:before,
*:after {
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
	margin: 0;
	padding: 0;
	box-sizing: inherit;
}

body {
	font-family: $font-family-base;
	font-weight: $font-weight-normal;
	line-height: $line-height-base;
	background-color: $body-bg;
	box-sizing: border-box;
	@include font-size($font-size-base);
}
:selection {
	background: rgba(141, 200, 232, 0.85); //#b3d4fc;
	text-shadow: none;
}

:focus {
	//outline: none; //move to base.scss?
}
::-moz-focus-inner {
	border: 0; //move to base.scss
}



h1, h2, h3, h4, h5, h6 {
    // Allow headings to be focused programatically without showing the focus outline. 
    // This should be helpful for orienting screenreader users without being ugly for everyone else.
    &[tabIndex="-1"]:focus {
        outline: 0;
    }
}
/*
 * Remove the gap between audio, canvas, iframes,
 * images, videos and the bottom of their containers:
 * https://github.com/h5bp/html5-boilerplate/issues/440
 */

audio,
canvas,
iframe,
img,
svg,
video {
	vertical-align: middle;
}

// move to helpers?
.center {
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
}
.truncate {
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	//need to define the width and block level to work
}
.eula {
	font-size:90%;
	.indent {
		margin-left: 3rem;
	}
	i {
		// font-style: italic;
	}
}
.eula-actions {
	border:1px solid $acp_red;
	padding:2rem;
	background-color:$white;
	>form {
		display:flex;
		align-items:center;
		justify-content:space-around;
	}
}
