.table-modal {
	.content-table {
		caption {
			.table-number {
				display: none;
			}
		}
	}
}

//added for scrolling large tables
.table-wrapper {
	overflow-x: auto;
	border-bottom: 1px solid $gray-800;
}

table.table {
	width: 100%;
	//max-width: 100%;
	display: table;
	border-collapse: collapse;
	background-color: var(--table-background, $white);
	> * {
		color: $text-color;
	}

	caption {
		caption-side: top;
		margin: 0;
		text-align: left;

		p {
			margin: 0;
		}
	}

	th,
	td {
		padding: 0.8rem 1rem;
		background-color: var(--table-background, $white);
		p {
			margin: 1.5rem 0 0 0;
			&.cell.li {
				padding-left: 1rem;
			}
			&:first-child {
				margin-top: 0;
			}
		}
	}
	th {
		font-weight: 500;
		text-align: left;
	}
	td {
		border-bottom: 1px solid $gray-300;
		vertical-align: top;
		text-align: left;
		//background-color: #fff;
	}

	thead tr th {
		text-align: left;
		border-bottom: 2px solid $gray-500;
	}
	tfoot {
		color: $text-color;
	}
}

.question-content table {
	caption {
		font-style: italic;
		background-color: var(--card-background, $white);
		box-sizing: border-box;
	}
	th,
	td {
		p:first-child {
			margin: 0;
		}
	}
}

#tables {
	.table-index {
		li {
			position: relative;
			clear: left;
			line-height: 1.25rem;
			padding: 0.5rem 0 0.5rem;
			border-top: 1px dotted $gray-200;
		}
	}
}

.content-table {
	&.pane {
		overflow-x: auto;
	}

	table {
		display: table;
		font-size: 1.4rem;
		width: 100%;
		border-collapse: collpase;
		empty-cells: show;
		border: none;
		background-color: var(--table-background, $white);
		overflow: auto;
		margin-bottom: 0;
	}
	.modal & table {
		border: 0;
		tbody {
			border: 0;
		}
	}
	caption p{
		//margin: 0;
		font-size: $font-size-lg;
		font-style: normal;
		//line-height: 1rem;
		text-align: left;
		b {
			display: block;
			font-weight: 800;
			margin-bottom: 1rem;
		}
		p {
			margin-bottom: 0.5rem;
		}
	}
	/* Headers */
	//.th, .thead td, th, .list tbody th				{ padding:0 10px;border:1px solid $blk_3;border-bottom:0;border-left:0;font-weight:400;color:#fff;background-color:$blk_2;text-align:left;vertical-align:top;  }
	tr th {
		padding: 1rem;
	}
	.th p,
	.thead td p,
	th p {
		margin-bottom: 0;
		line-height: 2rem;
		font-weight: 600;
	}
	th,
	.th.sub,
	tbody th {
		border-top: 2px solid $gray-500;
		border-bottom: 2px solid $gray-500;
		text-align: left;
		vertical-align: top;
		font-weight: 600;
		background-color: var(--table-background, $white);
	}
	.th.sub.a,
	.sub.a,
	.sub-a td {
		border: 1px solid $gray-500;
		color: $text-color;
		background-color: $gray-200;
	}
	.th.sub.b,
	.sub.b,
	.sub-b td {
		background: transparent;
		border-color: $gray-500;
	}
	.th.sub.c,
	.sub.c,
	.sub-c td {
		background: transparent;
		border-color: $gray-500;
	}
	.th.sub.d,
	.sub.d,
	.sub-d td {
		background: transparent;
		//font-size: 1em;
		font-weight: bold;
		color: $text-color;
		border-color: $gray-400;
	}
	tbody {
		width: 100%;
		border: none;
		border-top: 2px solid $gray-500;
	}
	/* Cells */
	td {
		vertical-align: top;
		border: 1px solid $gray-400;
		border-bottom: 0;
		border-right: 0;
		padding: 1rem;
		zoom: 1;
	}
	td p {
		margin: 0;
	}
	td:first-child,
	th:first-child {
		border-left: 0;
	}
	td:last-child,
	th:last-child {
		border-right: 0;
	}

	.cell.li {
		padding-left: 1rem;
	}
	.cell.c {
		text-align: center;
	}
	.cell.lii {
		padding-left: 1.5rem;
		text-align: left;
	}
	.cell.liii {
		padding-left: 2rem;
		text-align: left;
	}
	.col.hd.l {
		text-align: left;
		padding: 0;
	}
	.col.hd.c {
		text-align: center;
	}

	/* Rows */
	.alt,
	.alt td,
	.row-odd,
	.row-odd td {
		background-color: var(--table-background-odd, $gray-200);
	}
	.highlight {
		background-color: mksap-color("acp_gold");
	}

	tr.r_grp td,
	tbody tr:first-child td {
		border-bottom: 0;
	}
	tr.r_grp.child td:first-child {
		padding-left: 2.75rem;
	}
	tr.r_grp.end {
		border-bottom: 1px solid #ddd;
	}

	tr.tfoot,
	tfoot td {
		border-top: 2px solid $gray-500;
		background-color: var(--table-background, $white);
		font-size: 1.4rem;
	}

	// Columns & Rows
	//
	// ?:	td.c_#	==  common class for each cell sharing a column
	// ?:	tr.r_#	==	unique class for each row
	//

	/* Borders */
	.ribs.all {
		border: 1px solid #ddd;
	}
	.ribs.v {
		border-right: 1px solid #ddd;
		border-left: 1px solid #ddd;
	}
	.ribs.h {
		border-top: 1px solid #ddd;
		border-bottom: 1px solid #ddd;
	}

	.ribs.none {
		border: 0;
	}
	.ribs.v.no {
		border-right: 0;
		border-left: 0;
	}
	.ribs.h.no {
		border-top: 0;
		border-bottom: 0;
	}

	/* Text */
	.txt-l {
		text-align: left;
	}
	.txt-r {
		text-align: right;
	}
	.txt-c {
		text-align: center;
	}

	/* for simulation */
	td ul,
	td li {
		list-style-type: none;
		margin: 0;
		padding: 0;
	}

	.footnotes {
		//color: $text-muted;
		font-weight: $font-weight-light;
		border-top: 1px solid $gray-800;
		list-style-type: none;
		margin: 0;
		//padding: 1em 2em;
		li {
			font-size: 1.4rem;
			list-style-type: none;
			margin: 0;
			padding: 0.5rem;
			border-top: 1px dotted #ccc;
			&:first-child {
				border-top: 0;
			}
		}
	}
}

/* Table Exceptions */

.inline-table {
	position: relative;
	margin: 2rem 0;
	border: 1px solid $gray-400;
	// overflow: hidden;
	background-color: var(--table-background, $white);
	clear: both;
	caption {
		padding: 1rem;
	}
	.table-wrapper {
		border: 0;
	}
	.close {
		position: absolute;
		top: 0;
		right: 0;
		border-radius: 50px;
		padding:.5rem 1.5rem;
		margin:0;
	}
}

/* Submissions - CME/MOC */

table.credit-table,
.cmemoc table {
	border-collapse: collapse;
	margin: 0 0 3rem 0;
	th {
		border-bottom: 2px solid $gray-500;
		font-weight: 600;
	}
	th:first-child {
		text-align: left;
	}
	tbody th {
		padding-top: 12px;
	}
	td {
		border-top: 1px solid $gray-400;
		padding: 4px 8px;
		text-align: center;
	}
	td:first-child {
		text-align: left;
	}
	td.points,
	td.credits {
		font-weight: 600;
	}
	tr:first-child td {
		border: medium none;
	}
	tr.subtotal td {
		background-color: $gray-200;
	}
	tr.total td {
		border-top: 2px solid $gray-500;
		font-weight: 600;
	}
}
