.card {
	background-color: var(--card-background, $white);
	color: var(--card-color, $text-color);
	box-shadow: $box-shadow-sm;
	.card-header {
		background-color: var(--card-background, $white);
		color: var(--card-color, $text-color);
		border-bottom: 0;
		font-weight: 300;
		@include font-size($font-size-lg);
		padding-top: $card-spacer-x;
		display: flex;
		align-items: center;
	}
	.subtitle {
		font-weight: 300;
		color: var(--text-muted, $gray-700);
	}

	// Add themed borders. This should probably be handled differently!
	&.assignment-theme,
	&.boardbasic-theme,
	&.flashcard-theme,
	&.learningplan-theme,
	&.question-theme,
	&.quiz-theme,
	&.setting-theme,
	&.submission-theme,
	&.subspecialty-theme,
	&.text-theme,
	&.vdx-theme {
		border-color: var(--theme-primary);
	}
	&.resume {
		background-color: var(--theme-primary);
		color: var(--theme-primary-text);//mksap-color("white");
		a {
			color: var(--theme-primary-text);
		}
	}
}

.card-deck {
	display: flex;
	flex-direction: column;

	.card {
		margin-bottom: $card-deck-margin;
	}

	@include media-breakpoint-up(md) {
		flex-flow: row wrap;
		margin-right: -$card-deck-margin;
		margin-left: -$card-deck-margin;

		.card {
			display: flex;
			// Flexbugs #4: https://github.com/philipwalton/flexbugs#flexbug-4
			flex: 1 0 0%;
			flex-direction: column;
			margin-right: $card-deck-margin;
			margin-bottom: 0; // Override the default
			margin-left: $card-deck-margin;
		}
	}
}
.card-body + .list-group-flush {
	border-top: none;
}

//used on dyl

.scrolling.card {
	min-height: 0;
	max-height: calc(100vh - 40rem);
	overflow-y: auto;
}

.card {
	&.border.border-question-theme {
		border:2px solid $acp_blue_dark !important;
		box-shadow:none;
	}
}
