.pagination {
	display: flex;
	height: $pagination-height;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	flex-shrink: 0;
	box-sizing: border-box;
	background-color: var(--card-background, $white); //$nav-bar-bg-color;
	box-shadow: 0 -8px 6px -6px $gray-200;
	@include fix-left-next-to-navbar;

	.main-toolbox-drawer-open & {
		right: $toolbox-right-drawer-width;
	}

	ul.pagination-content {
		box-sizing: border-box;
		list-style: none;
		padding: 0;
		margin: 0;
		display: flex;
		justify-content: space-between;
		width: 100%;
		&:first-child {
			padding-left: 3%;

			@include media-breakpoint-up(sm) {
				padding-left: 5%;
			}
		}
		&:last-child {
			padding-right: 3%;

			@include media-breakpoint-up(sm) {
				padding-right: 5%;
			}
		}
	}
	.pagination-item {
		display: flex;
		align-items: center;
		a {
			color: mksap-color("acp_blue_mid");
			padding: 0.5rem 1rem;
			border-right: 0;
			text-decoration: none;
			border-radius: 5rem;
			text-align: center;
			min-width: 3.5rem;
			min-height: 2rem;
			display: flex;
			flex-flow: row nowrap;
			align-items: center;
			justify-content: center;


			&:not(:disabled):not(.disabled):focus-visible,
			&:not(:disabled):not(.disabled).focus-visible,
			&:not(:disabled):not(.disabled):visited:focus-visible,
			&:not(:disabled):not(.disabled):visited.focus-visible {
				outline:0;
				border-radius:$card-border-radius;
				box-shadow: $input-btn-focus-box-shadow;
			}

			&:hover {
				background-color: $pagination-hover-bg;
				color: $pagination-hover-color;
			}
		}
		&.no {
			font-weight: normal;
		}
		&.previous {
			white-space: nowrap;
			a {
				border:1px solid $link-color;
			}
		}
		&.next {
			white-space: nowrap;
			a {
				border:1px solid $link-color;
			}

		}
		&.out-of-range {
			font-weight: bold;
			color: $gray-400;
			padding: 5px;
			&:hover {
				color: $gray-400;
			}
		}
		&.active a {
			background-color: #fff;
			border: 1px solid $gray-400;
			margin: 0 1.5rem;
			color: $gray-700;
			border-radius: 0;

			&:hover {
				background-color: #fff;
				cursor: default;
				color: $gray-700;
			}
		}
	}
}

@media (max-width: 767.98px) {
	.pagination {
		left: 0;
		.pagination-item.previous,
		.pagination-item.next {
			padding: 0;
		}
		ul.pagination-content {
			justify-content: space-between;
			width: 100%;

			.page-item:not(.active) {
				display: none;
			}
			li.pagination-item.out-of-range {
				display: none;
			}
		}
	}
}

//remove ellipses out-of-range link styles for now
li.pagination-item.out-of-range a {
	color: $gray-700;
	cursor: default;
	&:hover {
		background-color: rgba(0, 160, 223, 0);
		color: $gray-700;
	}
}
