		"success": day-night($correct,#409144),
		"wrong": day-night($wrong,#E87878), ////#D96363
		"danger": day-night($wrong,#E87878),
		"unsure": $unsure,
		"info": day-night($acp_blue_mid,#2292bf),
		"warning": $acp_orange,
		"answered": day-night($slate, #5F809B),
		"link": day-night($acp_blue_mid, #007CF0),
		"home-theme": day-night($acp_blue_dark, #1759D3),
		"text-theme": day-night($acp_green, #008f75),
		"question-theme": day-night($acp_blue_dark, #1759D3),//$acp_blue_dark,
		"flashcard-theme": $acp_magenta,
		"quiz-theme": day-night($acp_violet,#9536B0),
		"assignment-theme": day-night($acp_orange,#c2660a),
		"boardbasic-theme": $acp_brown_mid,//$acp_violet_mid,
		"learningplan-theme": day-night($acp_blue_light,#0a87b8),
		"submission-theme": day-night($acp_green, #008f75),
