		"acp_blue_dark": day-night($acp_blue_dark, #235dc7),
		"acp_blue_mid": day-night($acp_blue_mid,#007CF0),
		"acp_blue_light": day-night($acp_blue_light,#2292bf),
		"acp_blue_lightest": day-night($acp_blue_lightest, $acp_blue_light),
		"acp_red": day-night($acp_red, #D96363),
		"acp_magenta": $acp_magenta,
		"acp_orange": day-night($acp_orange,#c2660a),
		"acp_yellow": $acp_yellow,
		"acp_gold": $acp_gold,
		"acp_violet": day-night($acp_violet,#B967D0),
		"acp_violet_mid": $acp_violet_mid,
		"acp_brown": $acp_brown,
		"acp_brown_mid": $acp_brown_mid,
		"acp_brown_light": $acp_brown_light,
		"text-color": day-night($text-color, #efefef),
		"search-term-highlight-bg": day-night(rgba($acp_blue_light, 0.2), rgba(#116485, 0.7)),
		"slate": day-night($slate, #5F809B),
		"white": day-night($white, $gray-300),
		"gray-100":day-night($gray-100, $white), //#f0f0f0;
		"gray-200": day-night($gray-200, $gray-100), //#e0e0e0;
		"gray-300": day-night($gray-300, $gray-200), //#d0d0d0;
		"gray-400":day-night($gray-400, $gray-300), //#b8b8b8;
		"gray-500":day-night($gray-500, $gray-400), //#787878;
		"gray-600": day-night($gray-600, $gray-500), //#606060;
		"gray-700": day-night($gray-700, $gray-600), //#484848;
		"gray-800": day-night($gray-800, $gray-700), //#303030;
		"gray-900":day-night($gray-900, $gray-800), //#181818;
		"black": day-night($black, $gray-800),
		"link-color": day-night($acp_blue_mid, #007CF0),
		"text-muted": day-night($text-muted, $gray-400),
		"input-bg":day-night($gray-100, $gray-700),
		"float-bg":day-night($gray-100, $gray-800),
		"card-background":day-night($white, #212121),
		"table-background":day-night($white, #212121),
		"table-background-odd":day-night($gray-200, $gray-800),
		"submission-not-earned": $gray-400,
		"submission-claimed": $correct,
		"submission-ready-to-claim": $acp_blue_mid,
		"question-chart-incomplete": $gray-400,
	)
);

// Colors listed in $page-themes will get helper classes (e.g. text-theme) defined to override the `primary` color on a page
$page-themes: (
	home-theme,
	text-theme,
	question-theme,
	flashcard-theme,
	quiz-theme,
	assignment-theme,
	boardbasic-theme,
	learningplan-theme,
	submission-theme,
	setting-theme,
	help-theme,
	frontmatter-theme
);

$night-mode-bg:#121212;
$night-mode-surface:#212121;//#282828;
$night-mode-float:#373737;
$night-mode-element:#dadada;
$night-mode-color:#efefef;

body.nightMode {
	font-weight:300;
}
.nightMode {

	background-color: $night-mode-bg;
	color: $night-mode-color;
	.modal-content{
		background-color: $night-mode-bg;
	}
	.pagination,
	.sidebar-sticky {
		background-color: #000;
	}
	.pagination {
		box-shadow: 0 -10px 20px -6px $gray-800;
	}
	.card,
	.card .card-header,
	.list-group-item,
	aside.keypoints,
	.toolbox-toggle,
	.toolbox-right-drawer,
	.toolbox-content .nav-pills .nav-link,
	.question-content > .timer__wrapper {
		background:$night-mode-surface;
		box-shadow:none;
	&.border,.table-bordered,.table-bordered th, .table-bordered td {
			border:1px solid $gray-700 !important;
		}
	}
	.btn-outline-primary, .form-control {
		background:$night-mode-element;
	}
	.tooltip-inner,
	.dropdown-menu, .badge
	 {
		background:$night-mode-float;
		box-shadow:none;
	}
	.list-group-item {
		border-color:$gray-700;
	}
	.progress__graph.mcq .progress__bar {
		box-shadow:none;
	}
	.modal-backdrop.show {
		opacity:0.85;
	}
	.toolbox-toggle .nav-pills .nav-item:first-child {
		border-bottom-color:$gray-600;
	}
	.toolbox-toggle .nav-pills .nav-item:last-child {
		border-top-color:$gray-600;
	}
	.sidebar-sticky .sidebar-text {
		font-weight:400;
	}
	.flashcards .flashcard__prompt {
		background-color:transparent;
		&:hover {
			background-color:$slate;
		}
	}
	//--text-color: color-yiq(#000);//#{$gray-100};
	//--card-background: $night-mode-surface;
	//--card-color: $night-mode-color;//#{$gray-100};
	//--component-active-color: purple;
	//--list-group-bg: $night-mode-surface;
	//--list-group-border-color:#{$gray-100};
	//--table-background: #{$gray-800};
	//--table-background-odd: #{$night-mode-surface};
	//--modal-background: #{$gray-800};
	//--text-muted: #{$gray-400};

}


// Define the "page-theme" class for each color in $page-themes
@each $color-name in $page-themes {
	@if map-has-key($mksap-theme-colors, $color-name) {
		.#{$color-name} {
			@include page-theme($color-name);
		}
	} @else {
		@warn "tried to add page-theme for no-existent color: #{$color-name}";
	}
}
