$stepper-circle-width: 2.4rem;
$stepper-left-padding: 2.4rem;

.quiz .title {
	.subtitle {
		display: block;
		font-size: 80%;
		color: $slate;
	}
}
//start of stepper styles
.stepper__step__title {
	margin-left: -14px; /*so the ripple is 24 to the left of the number*/
	padding-top: 12px;
	padding-left: calc(
		36px + 24px
	); /*per spec 12px from step counter, undoes the -24 margin*/
	padding-bottom: 12px;
	display: block;
	font-size: 1.8rem;
	font-weight: $font-weight-bolder;
	font-family: $font-family-base;
}

.stepper__tools {
	padding: 1rem 0rem;
	display: flex;
	flex: 1;
	margin: 0 -8px;
	flex-flow: row nowrap;
	align-items: center;
	justify-content: flex-start;
	width: 100%;
	& > * {
		margin: 0 8px;
	}
	.btn-link {
		flex: 0 1 auto;
	}
	.divider {
		width: 2px;
		margin: 6px 0;
		height: 1.6rem;
		background: $gray-600;
	}
}
.stepper__step--error,
.stepper__step--summary {
	display: none;
	margin-left: 3px;
	font-size: 85%;
	font-weight: lighter;
}

ol.stepper {
	font-family: $font-family-base;
	padding-left: $stepper-left-padding;
	list-style-type: none;
	counter-reset: section;
	overflow-y: auto;
	overflow-x: hidden;
	padding: 0;
	li.stepper__step {
		position: relative;
		width: 100%;
		&.stepper__step--active {
			.stepper__step__title {
				color: $text-color;
				font-weight: 700;
				.stepper__step--error {
					color: mksap-color("acp_red");
				}
				.stepper__step--summary {
					color: $gray-800;
					font-weight: lighter;
				}
			}
		}
		&.stepper__step--done {
			.stepper__step__title {
				color: mksap-color("slate");
				font-weight: 500;
				.stepper__step--summary {
					display: block;
				}
			}
		}
		.stepper__step--helper-text {
			font-size: 80%;
			color: $gray-400;
		}
		&.stepper__step--active.stepper__step--wrong {
			.stepper__step__title {
				color: mksap-color("acp_red");
				font-weight: 500;
				.stepper__step--error {
					display: block;
				}
			}
		}
		&.stepper__step--done {
			.stepper__step__title {
				color: mksap-color("slate");
				font-weight: 500;
			}
		}
	}
}
ol.stepper {
	list-style: none;
	counter-reset: steps;
	li {
		counter-increment: steps;
		position: relative;
		&::before {
			content: counter(steps);
			font-family: $font-family-base;
			position: absolute;
			top: 1rem;
			margin-left: 0.5rem;
			background-color:rgba($acp_violet, .5);
			//@include vary-with-night-mode {
				//background: lighten(mksap-color-raw("acp_violet"), 35%);
			//}
			color: $text-color;
			width: $stepper-circle-width;
			height: $stepper-circle-width;
			border-radius: 50%;
			display: inline-grid;
			place-items: center;
			line-height: $stepper-circle-width;
		}
	}
}
/* color of active stepper circle*/
.stepper .stepper__step.stepper__step--active:before {
	background-color: mksap-color("acp_violet");
	color: #fff;
}

.stepper > .stepper__step:not(:last-of-type) {
	margin-bottom: 0.5rem;
	transition: margin-bottom 0.4s;
}

.stepper__step.stepper__step--done:before {
	//background:url(/images/svg-icons/checkmark.svg);
	background: url("data:image/svg+xml;base64,")
		5px 5px no-repeat;
	text-indent: -9999px;
	background-size: 12px;
	background-position: center;
	background-color: $success;
}

.stepper__step.stepper__step--wrong:before {
	background: url("data:image/svg+xml;base64,")
		6px 4px no-repeat;
	background-color: mksap-color("acp_red") !important; /*todo theme*/
	background-size: 16px;
	text-indent: -9999px;
	background-position: center;
}

.stepper__step__content {
	position: relative;
	max-height: 0;
	transition: max-height 0.5s cubic-bezier(0, 1, 0, 1);
	width: inherit;
	overflow: hidden;
	padding-left: 4.5rem;
	box-sizing: border-box;
	font-family: $font-family-base;
	p {
		margin-top: 0;
	}
}

/* shows the step if it's marked as active, animates it */
.stepper__step.stepper__step--active .stepper__step__content {
	max-height: 9999px;
	transition: max-height 1s ease-in-out;
	display: flex;
	flex-flow: column nowrap;
	justify-content: flex-start;
	padding-bottom: 1rem;
}
.stepper__step stepper__step--active .stepper__step__content {
	.mdc-form-field {
		margin-bottom: 1.6rem;
	}
}

/*applies vertical line */
.stepper > .stepper__step:not(:last-of-type):after {
	content: "";
	position: absolute;
	top: 46px;
	left: 14.5px;
	width: 1px;
	height: 40%;
	height: calc(100% - 38px);
	background-color: $border-color;
	-webkit-transition: all 0.4s;
	transition: all 0.4s;
}

.stepper__step__content__actions {
	padding-top: 1rem;
	.btn {
		margin: 0.5rem;
	}
}
.stepper__step .stepper__step__label,
.stepper__step--wrong .stepper__step__label {
	display: none;
}
.stepper__step--done .stepper__step__label {
	display: block;
	color: #00a0df;
}
.form-group {
	&__row {
		display: flex;
		flex-flow: row nowrap;
	}

	&--column {
		display: flex;
		flex-flow: column nowrap;
		margin: 1rem 0 0.5rem;
		width: 100%;
		p {
			margin-bottom: 0.5rem;
		}
		.mdc-form-field + .mdc-form-field {
			margin-left: 0;
		}
		.mdc-form-field {
			label {
				display: flex;
				flex-flow: row nowrap;
				align-items: center;
				justify-content: space-between;
				width: 100%;
				.label {
					width: 40%;
				}
				.count {
					width: 60%;
					text-align: left;
				}
			}
		}
	}
}

