// path to cme/moc horizontal stepper
.stepper.horizontal {
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	justify-content: space-around;
	.stepper__step {
		display: flex;
		flex-flow: column nowrap;
		align-items: center;
		justify-content: center;
		width: 25%;
		margin: 1em;
		height: 150px;
	}
	.stepper__step__title {
		font-size: 0.75em;
		text-align: center;
		line-height: 1.5em;
		margin-top: 3.5em;
		margin-left: 0;
		padding-left: 0;
		.stepper__step--error {
			display: none;
		}
	}
	& > .stepper__step:not(:last-of-type):after {
		top: 26px;
		left: 80%;
		width: calc(100% - 120px);
		width: 40%;
		height: 1px;
	}

	.stepper__step:before {
		font-size: 16px;
		position: absolute;
		top: 12px;
		text-align: center;
	}
}
