a.sidebar-collapse {
	display: none;
}
//only show sidebar laptop and up
@include media-breakpoint-up(md) {
	.sidebar {
		width: $navDrawer-width;
		//transition: opacity 0.2s linear, visibility 0.1s, ease-in;
		flex-shrink: 0;
		a.sidebar-collapse {
			//margin-left: 1rem;
			display: flex;
			@include font-size($font-size-base);
			.svg-icon {
				fill: white;
				flex-shrink: 0;
			}
			&.btn-link:not(:disabled):not(.disabled):active,
			&.btn-link:not(:disabled):not(.disabled):hover {
				.svg-icon {
					fill: white;
				}
			}
		}
	}

	.drawer-collapsed .sidebar {
		width: $navDrawer-collapse;
		overflow: hidden;

		.flex-column {
			display: flex;
			justify-content: center;
			align-items: center;
		}
		.sidebar-sticky {
			width: $navDrawer-collapse;
			//transition: opacity 0.2s linear, visibility 0.1s, width 0.1s ease-in;
			z-index: 2000; //higher than the subspecialty menu
		}
		.svg-arrow-slim-left-circle {
			transform: rotate(180deg);
			fill: white;
			margin-right:0;
		}
		a.sidebar-collapse {
			margin: 0;
			width: 100%;
			display: flex;
			flex-flow: row nowrap;
			align-items: center;
			justify-content: center;
			border-radius: 0;
			border-color: transparent;
			&.btn-link:not(:disabled):not(.disabled):active .svg-icon {
				fill: white;
			}
			&:active,
			&:hover {
				cursor: pointer;
				.svg-icon {
					fill: white;
				}
			}
		}
		.navbar-brand-large,
		.sidebar-text {
			display: none;
			transition: all 0.2s;
			white-space: nowrap;
		}
		.navbar-brand-small {
			display: flex;
			margin: 0;
		}
		li.nav-item {
			width: 100%;
			.nav-link {
				flex-flow: column nowrap;
				justify-content: center;
				text-decoration: none;
				.svg-icon {
					margin-right: 0;
					width: 2rem;
					height: 2rem;
				}
			}
		}
		.user-small {
			display: block;
			padding-top: 0.25rem;
			font-size: 1.2rem;
			.svg-icon {
				margin-right: 0;
			}
		}
	}
}
//move sidebar off canvas on tablet size and down
@include media-breakpoint-down(md) {
	.navbar-offcanvas {
		position: fixed;
		left: (-$navDrawer-width);
		top: $top-app-bar-height;
		z-index: 999;
		top: 0;
		bottom: 0;
		-webkit-overflow-scrolling: touch;
		transition: visibility 0s, opacity 0.15s linear;

		opacity: 0;
		//display: none;
		visibility: hidden;
		&.in {
			box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
			opacity: 1;
			//display: block;
			visibility: visible;
			left: 0;
		}
	}

	//overlay only on tablet size and down
	.screen-overlay {
		height: 100%;
		z-index: 30;
		position: fixed;
		top: 0;
		left: 0;
		opacity: 0;
		visibility: hidden;
		background-color: rgba(34, 34, 34, 0.6);
		transition: opacity 0.2s linear, visibility 0.1s, width 1s ease-in;
	}
	.screen-overlay.show {
		transition: opacity 0.1s ease, width 0s;
		opacity: 1;
		width: 100%;
		visibility: visible;
	}
}

.btn.app-drawer-close {
	display: none;
}
//menu is full width on phone size
@include media-breakpoint-down(xs) {
	.btn.app-drawer-close {
		display: inline-flex;
	}
	.navbar-offcanvas.in {
		position: fixed;
		width: 100%;
		max-width: 100%;
		.sidebar-sticky {
			width: 100%;
		}
	}
}

// @media
.offcanvas-toggle {
	background: #f8f8f8;
	.icon-bar {
		background: #000;
		transition: all 0.25s ease-in-out;
	}
	// icon-bar
	&.is-open {
		.icon-bar:nth-child(1) {
			transform: rotate(45deg) translate(5px, 4px);
		}
		// icon-bar:nth-child(1)
		.icon-bar:nth-child(2) {
			opacity: 0;
		}
		// icon-bar:nth-child(2)
		.icon-bar:nth-child(3) {
			transform: rotate(-45deg) translate(4px, -4px);
		}
		// icon-bar:nth-child(3)
	}
	// is-open
}
// offcanvas-toggle class

//sidebar should stay in place when scrolling
.sidebar-sticky {
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	overflow-y: auto;
	overflow-x: hidden;
	height: 100%;
	width: $navDrawer-width;
	background-color: $nav-bar-bg-color;
	//padding-top: 0.5rem;
	transition: opacity 0.2s linear, visibility 0.1s, width 0.1s;
	hr {
		border-color: $gray-700;
	}
	.sidebar-text {
		color: mksap-color("white");
		a {
			color: mksap-color("acp_blue_light");
		}
	}
}
.navbar-brand {
	&.navbar-brand-large .svg-icon {
		width:100%;
		height:$top-app-bar-height;
		padding:15px;
	}
	&.navbar-brand-small {
		display: none;
		text-align: center;
		height:$top-app-bar-height;
		.svg-icon {
			width: 2.2rem;
			height: 2.2rem;
			margin:0.5rem;
		}
	}
}

//nav link styles in sidebar
.nav-link {
	padding: 0.75rem 1.5rem;
	color: mksap-color("white");
	text-decoration: none;
	height: 50px; //41.5799px;
	@include font-size($font-size-base);
	.sidebar-text {
		white-space: nowrap;
	}
	.svg-icon {
		margin-right: 1rem;
	}
	&:not(:disabled):not(.disabled):focus-visible,
	&:not(:disabled):not(.disabled).focus-visible,
	&:not(:disabled):not(.disabled):visited:focus-visible,
	&:not(:disabled):not(.disabled):visited.focus-visible {
		outline:0;
		border-radius:0;
		box-shadow: $input-btn-focus-box-shadow;
	}
	&:hover {
		cursor: pointer;
		background-color: rgba($list-group-hover-bg, 0.65);
		color: $white;
	}
	&:active,
	&.active {
		background-color: mksap-color("link-color");
		color: $white;
		.svg-icon {
			fill: $white;
		}
	}
}
.user-wrapper {
	@include font-size($font-size-base);
}
.sidebar-collapse {
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
}
.user-small {
	display: none;
}

