//highlight notifications
.notification {
	display:flex;
	flex-flow: row wrap;
	align-items: center;
	justify-content: space-between;
	padding: 0.5rem 0 0.5rem 1rem;
	height: 100%;
	border-radius: 5rem;
	cursor: default;
	@include background-opacity($highlighter-yellow, 0.3);
	&__inline {
		display: inline-flex;
		align-items: center;
		@include background-opacity($highlighter-yellow, 0.3);
		padding: 0.5rem 1rem;
		&.btn.btn-link.ripple:hover, &.btn-outline-link:hover, &.btn-link:hover {
			@include background-opacity($highlighter-yellow, 0.5);
			.svg-icon {
				fill:$warning;
			}
		}
	}

}

.inline-notification--icon {
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	justify-content: center;
	margin-right: 6px;
	margin-top: 2px;
	.svg-icon {
		fill: $warning;
		flex-shrink: 0;
	}
}
.inline-notification--message {
	color: $text-color;
	font-size: 0.9em;
	line-height: 1.5em;
}

//for question critiques, spans across
.critique,
.highlight-index {
	.inline-notification {
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: center;
		padding: 1px 4px;
	}
}

