@mixin hover {
	.app-allow-hover &:hover,
	&:active {
		@content;
	}
}
.question-content {
	padding-bottom: 6rem;
	margin-bottom: 6rem;
	article.question {
		display: flex;
		flex-flow: column nowrap;
		justify-content: center;
		.q_info,
		.q_mcq,
		.prompt,
		.answer {
			margin: 1rem auto;
			box-sizing: border-box;
			width: 100%;
		}

		// q_info section > stimulus
		.q_info .stimulus .header {
			display: flex;
			flex-flow: row nowrap;
			align-items: center;
			margin-right: 0.25rem;
			margin-bottom: 1rem;
			h1 {
				margin: 0;
			}
			.svg-icon {
				flex-shrink: 0;
				width: 2rem;
				height: 2rem;

				&.svg-check-circle {
					fill: mksap-color("correct");
					margin-right: 1rem;
				}
				&.svg-remove-circle {
					fill: mksap-color("wrong");
					margin-right: 1rem;
				}
				&.svg-hospitalist {
					margin-left: 1rem;
				}
			}
			.question-star-toggle {
				margin-left: 1rem;
			}
		}
		.table.lab-values {
			margin-bottom: 2rem;
			caption {
				padding: 0.5em 1.5rem;
				background-color: mksap-color("gray-200");
				color: mksap-color("gray-800");
			}
		}

		//hide all the icons during quizzes
		&.answered.exam-not-complete,
		&.hosp.exam-not-complete {
			.q_info .stimulus .header {
				.svg-icon.svg-check-circle,
				.svg-icon.svg-remove-circle,
				.svg-icon.svg-hospitalist {
					display: none;
				}
			}
		}
		// q_mcq section > presentation
		section.q_mcq .presentation .header {
			font-size: 2.5rem;
			//line-height: 3rem;
			padding-top: 1rem;
		}

		.exposition {
			.hvc {
				&:before {
					content: "";
					width: 1.6rem;
					height: 1.6rem;
					margin-right: 0.5em;
					display: inline-block;
					background: transparent url("../../images/hvc-icon-mono-org.svg")
						no-repeat center center;
					background-size: cover;
					vertical-align: -3px;
				}
			}
			.source {
				color: mksap-color(slate);
				font-weight: 600;
			}
			.objective {
				margin: 1rem 0;
			}
			.critique {
				.bibliography {
					font-size: 1.3rem;
				}
				.actions {
					display: flex;
					margin: 0.4rem 6rem 2rem;
					flex-flow: row nowrap;
					align-items: center;
					justify-content: flex-start;
				}
			}
			.q-aux {
				text-align: center;

				.last-updated {
					margin: 2rem 0;
					padding-top: 0.5rem;
					border-top: 1px solid $gray-500;
					color: $gray-500;
					text-align: left;
					font-size: 1.5rem;
				}
			}
		}
	}
}

//start of presentation form options styles

@mixin letter {
	background-color: transparent;
	border-radius: 50rem;
	border: 1px solid #aaa;
	color: inherit;
}
@mixin letter-selected {
	background-color: mksap-color("slate");
	border: 1px solid transparent;
	color: #fff;
}
@mixin letter-correct {
	background-color: mksap-color("correct");
	border: 1px solid transparent;
	color: #fff;
}
@mixin letter-incorrect {
	background-color: mksap-color("wrong");
	color: #fff;
	border: 1px solid transparent;
}

.presentation {
	&.card {
		box-shadow: none;
		border-radius: 0;
		@media (max-width: 575.98px) {
			margin-left:-25px;
			margin-right:-25px;
		}
	}
	.options {
		display: flex;
		flex-flow: column nowrap;
		width: 100%;
	}
}
article.question {
	h1 {
		.quiz-question-source {
			display: inline-block;
			clear: left;
			font-size: 0.3rem;
			line-height: 1.25rem;
		}
	}

	label,
	.peer {
		display: none;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: flex-start;
	}
	.peer {
		display: none;
		width: 0;
		opacity: 0;
		height: 0;
		margin-top: 0.3rem;
		text-align: right;
		overflow: hidden;
		.stats {
			display: table;
			width: 84%;
			height: auto;
			text-align: right;
		}
		.stat-label {
			display: none;
			letter-spacing: -0.01em;
			text-align: right;
			padding-right: 0.25rem;
		}
		.stat-bar {
			display: none;
			height:24px;
			position: relative;
			@include media-breakpoint-up(sm) {
				display: table-cell;
				background-color: mksap-color("slate");
			}
			.point {
				width: 0;
				height: 0;
				border-top: 5px solid transparent;
				border-bottom: 5px solid transparent;
				border-left: 5px solid mksap-color("slate");
				position: absolute;
				top: 6.5px;
				right: -5px;
				&:after {
					content: "";
					display: block;
					width: 0;
					height: 0;
					margin-top: -5px;
					margin-left: -5px;
					border-top: 5px solid transparent;
					border-bottom: 5px solid transparent;
					border-left: 5px solid mksap-color("slate");
				}
			}
		}
	}
	.option input {
		// Hide the radio buttons and position them absolutely so they don't interfere wth the layout
		@include sr-only();
		&:focus {
			~ label {
				box-shadow: 0px 0px 5px $input-focus-border-color;

			}
		}
	}
	label {
		padding-left: 1rem;
		padding-top:.5rem;
		border-radius: 4px;
		margin-bottom: 0;
		position: relative;

	}
	.answer-bubble,
	.answer-text {
		display: table-cell;
		position: relative;
	}
	.answer-text {
		padding-left: 0.5rem;
	}
	.answer-bubble {
		@include hover {
			cursor: pointer;
			.letter {
				background-color: mksap-color("acp_blue_lightest");
				border-color: mksap-color("acp_blue_lightest");
				color: mksap-color("white");
			}
		}
	}

	@include media-breakpoint-up(md) {
		label {
			@include hover {
				background-color: $list-group-hover-bg;
				cursor: pointer;
				.letter {
					background-color: mksap-color("acp_blue_lightest");
					border-color: mksap-color("acp_blue_lightest");
					color: mksap-color("white");
				}
			}
		}
	}

	.bubble {
		font-size: 1.6rem;
	}
	.option {
		//s_a = selected answer
		&.s_a {
			.stat-bar {
				background-color: $gray-800;
				border: none;
				color: #fff;
			}
			.bubble {
				@include letter-selected;
			}
		}

		&.s_a.r_a {
			background: mksap-color-alpha("correct", 0.25);
			pointer-events: none;
			.answer-text {
				color: mksap-color("text-color");
			}
		}
		&.r_a {
			//r_a = right answer
			label,
			label:hover {
				color: $text-color;//mksap-color("correct");
			}
			.stat-bar {
				background-color: mksap-color("correct");
				.point:after {
					border-left-color: mksap-color("correct");
				}
			}
			.bubble {
				border: 0.1rem solid mksap-color("correct");
				@include vary-with-night-mode {
					background: lighten(mksap-color-raw("correct"), 53%);
				}
				span {
					color: mksap-color("correct");
				}
			}
			.label,
			.stat-label {
				color: mksap-color("correct");
			}
		}
		&.w_a {
			//w_a = wrong answer
			label,
			label:hover {
				color: mksap-color("wrong");
			}
			.stat-bar {
				background-color: mksap-color("wrong");
				.point:after {
					border-left-color: mksap-color("wrong");
				}
			}
			.bubble {
				border: 0.1rem solid mksap-color("wrong");
				@include vary-with-night-mode {
					background: lighten(mksap-color-raw("wrong"), 53%);
				}
				span {
					color: mksap-color("wrong");
				}
			}
			.label,
			.stat-label {
				color: mksap-color("wrong");
			}
		}
	}

	&.answered {
		.stats,
		.stat-label {
			display: inline-flex;
			align-items: stretch;
			justify-content: flex-end;
		}
		label {
			pointer-events: none;
		}
		.option {
			display: table;
			width: 100%;
			@media (max-width: 575.98px) {
				padding: 0;
			}
			.peer {
				display: table-cell;
				opacity: 1;
				width: 36%;
				height: 100%;
				overflow: visible;
				@media (max-width: 575.98px) {
					width: 15%;
				}
			}
			@media (max-width: 575.98px) {
				.stat-bar {
					display: none;
				}
			}
		}
		.option label {
			cursor: auto;
		}
	}

	.invalidated-message {
		background-color: var(--card-background, $white);//#efefef;
		border:1px solid $warning;
		color: mksap-color("text-muted");
		padding: 1rem .5rem;
		font-size:1.5rem;
	}
}

.js-timer-region {
	display: none;
}
.app-exam-timer-in .js-timer-region {
	display: block;
}

.question.answered.exam-not-complete {
	.option .peer {
		display: none;
	}

	.option {
		label {
			width: auto;
			float: none;
		}
		&.r_a,
		&.w_a {
			label,
			label:hover {
				background: none !important;
				color: inherit !important;
			}
			.letter {
				@include letter;
			}
		}
		&.s_a {
			background-color: mksap-color-alpha("acp_blue_lightest", 0.2);
			border-radius: 0.2em;
			label {
				color: inherit !important;
			}
			.letter {
				@include letter-selected;
			}
		}
	}
}

.custom-quiz {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;

	.answered {
		.q_info h1 i.hide {
			display: inline;
			margin-right: 0.25rem;
			color: mksap-color("acp_blue_lightest");
		}
	}
}

.question-index-empty-state {
	$mk17-color: darkgray;
	$fc-color: mksap-color("acp_green_bright");
	$vdx-color: #007bac;
	$updates-color: darken($acp_gold, 10%);

	.abbr {
		float: left;
		font-weight: 300;
		font-size: 0.5em;
		margin-right: 1em;
		width: 2em;
		height: 2em;
		// line-height:2em;
		text-align: center;
		color: #fff;

		&.q-17 {
			background-color: $mk17-color;
			color: mksap-color("acp_gold");
		}
		&.q-fc {
			background-color: $fc-color;
		}
		&.q-vdx {
			background-color: $vdx-color;
			width: auto;
			padding: 0 0.5em;
		}
		&.q-updates {
			background-color: $updates-color;
		}
	}
	.wrapper {
		margin: 0;
		padding: 0;
		height: auto;
		background-color: #fff;
		box-shadow: 0 0 18px rgba(#000, 0.15);
		.img {
			display: block;
			margin-bottom: 1em;
			text-align: center;
		}
		h1 {
			margin-top: 0;
			text-align: center;
			color: mksap-color("acp_green_mid");
			.abbr {
				display: inline-block;
				position: relative;
			}
		}
		h3 {
			margin: 2rem 0 1rem;
			text-align: center;
		}

		//@include handheld {

		@include media-breakpoint-up(sm) {
			margin: 1em;
		}
		@include media-breakpoint-up(md) {
			margin: 2em auto;
			max-width: 648px;
		}
		@include media-breakpoint-up(lg) {
			margin: 2em auto;
		}

		.copy {
			padding: 2rem 2rem 0 2rem;
			ul {
				padding: 2em;
				margin: 0;
				background-color: #eee;
			}
		}
		.action {
			padding: 2rem 0;
			background-color: #fff;
			text-align: center;
			h3 {
				margin: 0 0 1rem;
				padding: 0 2rem;
			}
			a.btn {
				padding-left: 1.5rem;
				padding-right: 1.5rem;
				line-height: 1.35rem;
				.button-label {
					display: block;
				}
			}
		}
	}
}

// question id specific styles
article.question {
	&[data-question-id="mk19_b_pm_q070"] {
		@include media-breakpoint-up(md) {
			.critique .keypoints {
				width: 100%;
			}
		}
	}

	&.question-invalidated {
		.critique .keypoints, .objective {
			display: none;
		}
	}
}
