.masthead {
	.heading-primary {
		@include font-size($h1-font-size);
		line-height: $headings-line-height;
		font-weight: 600;
		color: var(--theme-primary);
		border: none;
		> .img-thumbnail {
			max-width: 6rem;
			margin-right: 1rem;
			align-self: flex-start;
		}
		> .svg-icon {
			margin-right: 1rem;
			@include font-size($h3-font-size);
		}
	}

	.heading-secondary {
		font-weight: 400;
		margin-left: 4rem;
		@include font-size($h5-font-size);
	}
}

.resume {
	@include font-size($font-size-base);
}
.hero-banner {
	position: relative;
	background-color: var(--theme-primary);
	backface-visibility: hidden;
	//transform: translate3d(0, 0, 0);
	background-image: linear-gradient(
		5deg,
		mksap-color-alpha("black", 0.15) 10%,
		var(--theme-primary) 90%
	);

	.hero-image {
		// Chrome and Safari have trouble combining a background image with a background gradient
		// that contains CSS custom props. To get around that, we add a separate element containing
		// the hero image as the background.
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;
		background-image: url("../../images/hero-transparent-bg.png");
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
	}
	.heading-primary,
	.heading-secondary {
		color: var(--theme-primary-text);
		margin-bottom:0.8rem;
	}
	.heading-secondary {
		text-shadow: 1px 1px 10px rgba($black, 0.5);
		letter-spacing: 0.1rem;
		margin-left: 0;
	}

	//background-image: url("../../images/hero-transparent-bg@2x.png"),linear-gradient(4deg, rgba(0, 113, 158, 1) 0%, rgba(1, 147, 205, 1) 100%);

	.masthead .heading-primary .svg-logo-acp-mksap19-w {
		height: 1.6em;
		width: 12em;
	}
	.masthead .card {
		.circle {
			border-radius: 50%;
			background-color:var(--theme-primary);
			display: flex;
			align-items: center;
			justify-content:center;
			width:50px;
			height:50px;
			margin-right:1rem;
			.svg-icon {
				fill:$white;
				width:2rem;
				height:2rem;
				margin-left:3px;
			}
		}
	}
}
