.inline-figure,
.inline-thrombectomy-series {
	position: relative;
	margin: 2em 0;

	.close {
		position: absolute;
		top: 0;
		right: 0;
		border-radius: 50px;
		padding:.5rem 1.5rem;
		margin:0;
	}
}

.zoomable-figure {
	display: flex;
	flex-direction: column;
	flex-grow: 1;
	.zoomable-figure__controls {
		margin-bottom: 1rem;
		display: flex;
		justify-content: center;
	}
	img {
		box-sizing: border-box;
		vertical-align: middle;
		//image-rendering: pixelated;
		object-fit: scale-down;
	}
	img[src$=".svg"] {
		padding: 1em;
		background-color: #fff;
	}
	.zoomable-figure__image-wrapper {
		position: relative;
		height: 70vh;
		overflow: auto;
	}
	.zoomable-figure__image-drag-surface {
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		width: 100%;
		background: #eee url("../../images/loading.gif") center center no-repeat;
		text-align: center;
	}

	&.zoomable-figure--zoomed {
		.zoomable-figure__image-drag-surface {
			overflow: scroll;
			img {
				cursor: grab;
				object-fit: contain;
			}
		}
	}
}

.figure-modal {
	.fg-header {
		.title {
			.figure-number {
				display: none;
			}
		}
	}
}

.figure {
	width: 100%;
	.fg-header {
		align-self: flex-start;
		padding: 0.5em 0;
		.title {
			margin-bottom: 0;
		}
	}

	.img-wrap {
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: center;
		padding: 0 1rem 2rem;

		background: url("../../images/loading.gif") center center no-repeat;

		&:before {
			// Needed to center tiny images vertically
			content: " ";
			display: inline-block;
			min-height: 10em;
			vertical-align: middle;
		}
	}
	img {
		box-sizing: border-box;
		vertical-align: middle;
		cursor: zoom-in;
		width: 100%;
	}
	img[src$=".png"] {
		image-rendering: auto;
	}
	img[src$=".svg"] {
		padding: 1em;
		background-color: #fff;
	}
	.legend {
		padding: 1em 0;
		line-height: 1.5em;
	}
	.fg-footer {
		margin:1rem auto 0;
		.footnote {
			font-weight: $font-weight-light;
		}

		sup {
			margin-right: 0.25em;
		}
		ul {
			list-style-type: none;
			margin: 0;
			padding: 0;
			li {
				list-style-type: none;
				margin: 0;
				padding: 0.5rem 0;
				border-top: 1px dotted #ccc;
				&:first-child {
					border-top: 0;
				}
			}
		}
	}
}

//figures in text and questions are styled differently
.inline-figure.inline-figure--question {
	border: none;
	background-color: transparent;
}
.inline-figure.inline-figure--text,
.inline-thrombectomy-series {
	border: 1px solid $gray-300;
	background-color: var(--card-background, $white);
	.fg-header,
	.fg-footer {
		padding: 1rem;
	}
}

.video-modal {
	.fg-header {
		.title {
			.video-number {
				display: none;
			}
		}
	}
}

.video-wrap {
	position: relative;
	display: flex;
	flex-flow: row nowrap;
	justify-content: center;
	video {
		width:fit-content;
		height:fit-content;
		object-fit: contain;
		background-color:$white;
	}
}
.transcript-wrapper {
	margin: 0 auto;
}
