@mixin hover {
	.app-allow-hover &:hover,
	&:active {
		@content;
	}
}

// ### TEXT

.topic {
	//padding: 0 1rem;
	overflow-x: auto;
	-ms-overflow-style: -ms-autohiding-scrollbar;
	//transition: all 0.25s ease-out;
	h1 {
		color:var(--theme-primary);
		@include font-size($h1-font-size);
		font-weight: $font-weight-bold;
		line-height: $line-height-base; //1.619;
		margin-bottom: $headings-margin-bottom;
	}
	h2 {
		@include font-size($h2-font-size);
		font-weight: $headings-font-weight;
		line-height: $headings-line-height;
		margin-bottom: $headings-margin-bottom;
	}
	h3 {
		@include font-size($h3-font-size);
		font-weight: $font-weight-light;
		line-height: $headings-line-height;
		margin-bottom: $headings-margin-bottom;
	}
	h4 {
		@include font-size($h4-font-size);
		font-weight: $font-weight-lighter;
		line-height: $headings-line-height;
	}
	h5 {
		@include font-size($h5-font-size);
		font-weight: $font-weight-normal;
		line-height: $headings-line-height;
	}
	p {
		@include font-size($rfs-base-font-size);
	}
	> .topic-content-wrapper {
		//max-width: $reading-width;
		margin: 0 auto;
		padding: 0;
		> section {
			padding: 0 0 0 0;
		}
	}
	.linear-next,
	.linear-prev {
		flex-flow: row wrap;
		white-space: normal;
	}
	.equation {
		word-wrap: break-word;
		text-align: center;
	}
	a.table,
	a.figure {
		white-space: nowrap;
		word-wrap: break-word;
		> .svg-icon {
			margin-left: 0.25em;
			width: 1rem;
			height: 1rem;
			vertical-align: middle;
			position: relative;
			top: -2px;
		}
		&:visited > .svg-icon {
			fill: $link-visited-color;
		}
	}
	//i don't know why we need this, removing for now
	//ul:not(.mk-list):not(.footnotes) {
	//margin-left: 2em;

	//}
	.inline-wrap {
		display: block;
		content: "";
		width: 100%;
		/*
		@include super-wide {
			clear: both;
		}*/
		&.inline-wrap--hidden {
			display: block;
			content: "";
			clear: both;
			visibility: hidden;
			height: 0;
		}
	}

	.new-window-link {
		display: inline-block;
		margin: 0 0 0 1rem;
		padding-left: 0.5rem;
		padding-right: 0.5rem;
		font-size: 1rem;
		font-weight: normal;
		border-radius: 222rem;
		border: 0.1rem solid lighten($acp_blue_lightest, 5%);
		text-transform: uppercase;
		text-align: center;

		&:after {
			content: "";
			width: 0.9em;
			height: 0.9em;
			margin-left: 0.5rem;
			margin-right: 0.15rem;
			display: inline-block;
			background: transparent url("../../images/icon-link-external.svg")
				no-repeat center center;
			background-size: cover;
			vertical-align: -1px;
		}
	}
	.related__list a{
		@include hover() {
			cursor:pointer;
		  }
	}

	.bibliography {
		display: flex-column;
		align-items: center;
		align-items: flex-start;
		padding-top: 2rem;
		.velocity-transition-group {
			width: 100%;
		}
		.toggle__header {
			display: flex;
			flex-flow: row nowrap;
			align-items: center;
		}
		.toggle__action {
			display: flex;
			align-items: center;
			color: $link-color;
			padding: 0.1rem 0.6rem;
			font-size: 1.6rem;
			&:hover {
				cursor: pointer;
				background-color: rgba(141, 200, 232, 0.1);
			}
		}
		.svg-icon {
			margin-right: 0.8rem;
		}
		h2 {
			margin-right: 1.6rem;
		}
		ul,
		li {
			margin: 0;
			padding: 0;
			list-style-type: none;
		}
		li {
			margin: 1rem 0 1rem 1rem;
			text-indent: -1rem;
			a {
				/* These are technically the same, but use both */
				overflow-wrap: break-word;
				word-wrap: break-word;
				word-break: break-word;

				/* Adds a hyphen where the word breaks, if supported (No Blink) */
				-ms-hyphens: auto;
				-moz-hyphens: auto;
				-webkit-hyphens: auto;
				hyphens: auto;
			}
		}
	}
	.last-updated {
		margin: 2rem 0;
		padding-top: 0.5rem;
		border-top: 1px solid $gray-500;
		color: $gray-500;
		text-align: left;
		font-size: 1.5rem;
	}
}
.modal {
	.topic > .topic-content-wrapper {
		max-width: 100%;
	}
}
b > small {
	font-weight:bold;
}
.text-muted {
	color: mksap-color("text-muted") !important;
	font-weight: 300;
}
