.preline {
	white-space: pre-line;
}
.form-control-plaintext {
	pointer-events: none;
	color: $text-color;
	&:focus {
		outline: none;
		pointer-events: none;
	}
}
textarea[readonly] {
	resize:none;
	height:auto;
}
.custom-switch .custom-control-label::before { cursor: pointer; }
.custom-switch .custom-control-label::after { cursor: pointer; }
.custom-switch .custom-control-input:disabled ~ .custom-control-label::before { cursor: not-allowed; }
.custom-switch .custom-control-input:disabled ~ .custom-control-label::after { cursor: not-allowed; }
//learning plan toggle button
$loader-size: 1.9em;
$check-height: $loader-size/2;
$check-width: $check-height/2;
$check-left: ($loader-size/3 + $loader-size/6);
$check-thickness: 3px;
$check-color: mksap-color("correct");

.checkmark {
	display: none;

	&.draw:after {
		animation-duration: 800ms;
		animation-timing-function: ease;
		animation-name: checkmark;
		transform: scaleX(-1) rotate(135deg);
	}

	&:after {
		opacity: 1;
		height: $check-height;
		width: $check-width;
		transform-origin: left top;
		border-right: $check-thickness solid $check-color;
		border-top: $check-thickness solid $check-color;
		content: "";
		left: -50%;
		top: 50%;
		position: absolute;
	}
}
//form checkbox
.form-check {
	position: relative;
	display: inline-flex;
	align-items: center;
}
.form-check-input {
	&:not(:disabled):not(.disabled):focus-visible,
	&:not(:disabled):not(.disabled).focus-visible,
	&:not(:disabled):not(.disabled):visited:focus-visible,
	&:not(:disabled):not(.disabled):visited.focus-visible {
		outline:0;
		border-color:$input-focus-border-color;
		@include box-shadow($input-box-shadow, $input-focus-box-shadow);

	}
	&[type="radio"] {
		border-radius:50%;
	}
}
.checkbox-container {
	input[type="checkbox"] {
		position: absolute;
		opacity: 0;
		top: 1rem;
	}

	label {
		cursor: pointer;
		margin: 0.5rem;
	}
}

.checkbox-container .checkbox {
	display: inline-block;
	width: 1.4rem;
	height: 1.4rem;
	background: #fff;
	border: 1px solid rgba(0, 0, 0, 0.25);
	border-radius: 3px;
	margin: 0;
	position: relative;
	flex-shrink: 0;
	&:after {
		opacity: 0;
		height: $check-height;
		width: $check-width;
		transform-origin: left top;
		border-right: $check-thickness solid $check-color;
		border-top: $check-thickness solid $check-color;
		content: "";
		left: 0;
		top: 7px;
		position: absolute;
		z-index: 3;
	}
}

.checkbox-container input[type="checkbox"]:checked {
	& ~ .checkbox {
		animation-delay: 0.1s;
		background-color: mksap-color-alpha("correct", 0.3);
		&:after {
			opacity: 1;
			animation-duration: 800ms;
			animation-timing-function: ease;
			animation-name: checkmark;
			transform: scaleX(-1) rotate(135deg);
		}
	}
}

.checkbox-container input[type="checkbox"]:focus {
	& ~ .checkbox {
		//outline: 1px solid $input-focus-border-color;
		//outline-offset: 1px;
		outline: none;
		box-shadow: $input-btn-focus-box-shadow;
	}
}

@keyframes checkmark {
	0% {
		height: 0;
		width: 0;
		opacity: 1;
	}
	20% {
		height: 0;
		width: $check-width;
		opacity: 1;
	}
	40% {
		height: $check-height;
		width: $check-width;
		opacity: 1;
	}
	100% {
		height: $check-height;
		width: $check-width;
		opacity: 1;
	}
}
