@mixin fix-left-next-to-navbar {
	left: $navDrawer-width;
	.drawer-collapsed & {
		left: $navDrawer-collapse;
	}

	@include media-breakpoint-down(md) {
		left: 0;

		.drawer-collapsed & {
			left: 0;
		}
	}
}
.navbar.text-white {
	height:6rem;
	img {
		fill:white;
		width:1.6rem;
		height:1.6rem;
	}
}
