#modal-root > div {
	// Add a z-index to the elements that wrap modals so that nested modals' overlays display properly
	position: relative;
	z-index: 0;
}

.header__close {
	.svg-icon {
		border-radius: 5em;
		color: mksap-color("acp_blue_light");
		width: 2.5em;
		height: 2.5em;
		padding: 10px;
		cursor: pointer;
		box-sizing: border-box;
	}
}

.modal {
	&.modal-inline {
		// Displays a modal in the flow of the content, rather than as a true modal.
		// This should really only be used for testing/stories.
		position: relative;
	}
	.modal-dialog {
		display: flex;
		max-height: calc(100% - 3.5rem);
		&.modal-full-height {
			height: 100%;
		}
	}
	.modal-header {
		height:auto;
		border:none;
		.modal-title {
			min-height:3.5rem;
			font-size:$font-size-lg;
			//width:90%;
			//width: calc(100% - ($font-size-base * $modal-header-padding-x));
			width: calc(100% - 5rem);//guess at close btn width
		}
	}
	.modal-body {
		overflow-y: auto;
		border: 0;
		margin: 0;
		overflow-x: hidden;
		& > .content-table,
		& > .figure {
			margin-top: -2rem;//move title up on modals with just figures and tables
		}
	}

	.jump-to-content a {
		display: flex;
		justify-content: flex-start;
		align-items: center;
		padding: 0.5em 1.5em;
		color: #fff;
		cursor: pointer;
		background-color: mksap-color("acp_blue_mid");
		color: white;
		.svg-icon {
			margin-left: 10px;
		}
		&:hover {
			background-color: darken($acp_blue_mid, 5%);
			text-decoration: none;
		}
	}
}
.modal-dialog-scrollable {
	.modal-header {
		border-bottom: 1px solid $border-color;
	}
}
.modal-footer {
	.btn:first-of-type {
		margin-right: 2rem;
	}
}
@media (max-width: 767.98px) {
	.modal {
	.modal-dialog, .modal-xl, .modal-lg {
		margin: 0;
		max-width: 100%;
		max-height: 100%;
		.modal-content {
			height: auto;
			min-height: 100%;
			border-radius: 0;
		  }
		}
	}
}
