.claimed {
	.svg-icon.svg-shape-square {
		fill: var(--submission-claimed);
	}
	&.btn-primary {
		@include mksap-button-variant("submission-claimed");
	}
}
.ready-to-claim {
	.svg-icon.svg-shape-square {
		fill: var(--submission-ready-to-claim);
	}
	&.btn-primary {
		@include mksap-button-variant("submission-ready-to-claim");
	}
}
//donut-chart

.donut-chart {
	position: relative;
	max-height: 5.5em;
	max-width: 5.5em;
	.donut-label {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		display: flex;
		flex-flow: column nowrap;
		align-items: center;
		font-weight: 600;
		color: var(--submission-claimed);
		span:last-child {
			font-size: 13px;
			font-weight: 200;
			color: $text-color;
		}
	}
}
