//higlighter color palette


//night mode higlighter color palette
$highlighter-yellow-night: scale-color(
	$highlighter-yellow,
	$lightness: 4.5%,
	$saturation: -5%
); //#ebef00;
$highlighter-orange-night: scale-color(
	$highlighter-orange,
	$lightness: 4.5%,
	$saturation: -10%
); //#ffa03e;
$highlighter-green-night: scale-color(
	$highlighter-green,
	$lightness: 4.5%,
	$saturation: -10%
); // #4ce62a;
$highlighter-pink-night: scale-color(
	$highlighter-pink,
	$lightness: 4.5%,
	$saturation: -5%
); //#ff2a9d;
$highlighter-purple-night: scale-color(
	$highlighter-purple,
	$lightness: 4.5%,
	$saturation: -20%
); // #bc13fe;
$highlighter-trash-night: scale-color(
	$highlighter-trash,
	$lightness: 5.5%,
	$saturation: -20%
); // #c8291e;
$highlighter-info-night: scale-color(
	$highlighter-info,
	$lightness: 5.5%,
	$saturation: -20%
); // #00a0df;

@mixin background-opacity($color, $opacity: 0.3) {
	background: $color; /* The Fallback */
	background: rgba($color, $opacity);
}
.highlight-wrapper {
	position: relative;
}

.highlight-background {
	background-repeat: no-repeat;
	background-size: 100% 0.55em;
	background-position: 0 90%;
	transition: background-size 0.25s ease-in;
}

//text selection colors
.highlighter {
	text-shadow: none;
	display: inline;
	box-decoration-break: clone;
	white-space: pre-wrap;
	word-wrap: break-word;
	&:hover {
		cursor: pointer;
	}
	&.highlight-active {
		cursor: default;
	}
	&.highlighter-yellow {
		@include background-opacity($highlighter-yellow, 0.4);
		@extend .highlight-background;
		&:hover,
		&:active,
		&.highlight-active {
			@include background-opacity($highlighter-yellow, 0.6);
		}
		&.highlight-pending {
			@include background-opacity($highlighter-yellow, 0.1);
		}
	}
	&.highlighter-orange {
		@include background-opacity($highlighter-orange, 0.4);
		@extend .highlight-background;
		&:hover,
		&:active,
		&.highlight-active {
			@include background-opacity($highlighter-orange, 0.5);
		}
		&.highlight-pendingg {
			@include background-opacity($highlighter-orange, 0.1);
		}
	}
	&.highlighter-green {
		@include background-opacity($highlighter-green, 0.35);
		@extend .highlight-background;
		&:hover,
		&:active,
		&.highlight-active {
			@include background-opacity($highlighter-green, 0.6);
		}
		&.highlight-pending {
			@include background-opacity($highlighter-green, 0.1);
		}
	}
	&.highlighter-pink {
		@include background-opacity($highlighter-pink, 0.3);
		@extend .highlight-background;
		&:hover,
		&:active,
		&.highlight-active {
			@include background-opacity($highlighter-pink, 0.5);
		}
		&.highlight-pending {
			@include background-opacity($highlighter-pink, 0.1);
		}
	}
	&.highlighter-purple {
		@include background-opacity($highlighter-purple, 0.28);
		@extend .highlight-background;
		&:hover,
		&:active,
		&.highlight-active {
			@include background-opacity($highlighter-purple, 0.4);
		}
		&.highlight-pending {
			@include background-opacity($highlighter-purple, 0.1);
		}
	}
}

.topic-content {
	.fab-highlight-button,
	.fab-highlight-menu {
		bottom: 4.5em;
	}
}
.question-content {
	.fab-highlight-button,
	.fab-highlight-menu {
		bottom: 4.5em;
	}
}
.fab-highlight-button {
	cursor: pointer;
	transition: box-shadow 0.28s cubic-bezier(0.4, 0, 0.2, 1),
		opacity 15ms linear 30ms;
	margin-bottom: constant(safe-area-inset-bottom);
	margin-bottom: env(safe-area-inset-bottom);
	position: fixed;
	right: calc(16.667% + var(--modal-right-offset, 0px)); //$reading-width;;
	//bottom:0;
	z-index: 5;
	animation: highlighter-fadein 200ms ease-in-out,
		highlight-bounce 650ms 250ms 1;

	.fab-highlight-button__hidden-text {
		// This styles the hidden text behind the add highlight FAB. We need this to prevent long presses in the
		// iOS app from selecting the text all the way at the bottom of the page.
		position: absolute;
		height: $fab-size;
		width: $fab-size;
		overflow: hidden;
		background: transparent;
		color: transparent;
		word-break: break-all;
	}
	.fab-notification {
		display: none;
	}
	.mk-fab {
		@include background-opacity($highlighter-info, 1);
		.svg-icon {
			fill: #444;
			//width: 1.5rem;
			//height: 1.5rem;
		}
	}
	&--highlighter-yellow {
		.mk-fab {
			background-color: lighten(desaturate($highlighter-yellow, 18.29), 18.82);
		}
	}
	&--highlighter-orange {
		.mk-fab {
			background-color: lighten(desaturate($highlighter-orange, 28.29), 24.82);
		}
	}
	&--highlighter-green {
		.mk-fab {
			background-color: lighten(desaturate($highlighter-green, 23.29), 18.82);
		}
	}
	&--highlighter-pink {
		.mk-fab {
			background-color: lighten(desaturate($highlighter-pink, 40.63), 29.22);
		}
	}
	&--highlighter-purple {
		.mk-fab {
			background-color: lighten(desaturate($highlighter-purple, 23.29), 26.82);
		}
	}
	&--disabled {
		&.fab-highlight-button--disabled {
			.mk-fab {
				background-color: $gray-300;
				padding: 0;
				animation: highlighter-fadein 200ms ease-in-out,
					highlight-bounce 650ms 250ms 1;
				.svg-icon {
					fill: #444;
					//width: 1.5em;
					//height: 1.5em;
				}
			}
			.fab-notification {
				display: block !important;
				padding: 0;
				position: absolute;
				left: 35px;
				bottom: -3px;
				width: 25px;
				height: 25px;
				border-radius: 50%;
				display: flex;
				align-items: center;
				justify-content: center;
				border: 1px solid #ccc;
				animation: highlighter-fadein 200ms ease-in-out,
					highlight-bounce 650ms 250ms 1;
				//box-shadow:$box-shadow-sm;
				.svg-icon {
					position: absolute;
					top:50%;
					left:50%;
					transform:translate(-45%, -45%);
					//margin: -8.5px 0 0 -8.5px;
				}
				&:hover {
					cursor: pointer;
				}
				&--alert {
					background-color: mksap-color("acp_magenta");
					.svg-icon {
						fill: #fff;
						width: 0.9rem;
						height: 0.9rem;
					}
				}
				&--info {
					//display: block !important;
					background-color: #eee;
					.svg-icon {
						fill: mksap-color("acp_blue_light");
						text-align: center;
					}
				}
			}
		}
	}
	&--highlighter-yellow {
		.mk-fab {
			background-color: lighten(desaturate($highlighter-yellow, 18.29), 18.82);
			.svg-icon {
				fill: $gray-900;
			}
		}
	}
	&--highlighter-orange {
		.mk-fab {
			background-color: lighten(desaturate($highlighter-orange, 28.29), 24.82);
			.svg-icon {
				fill: $gray-900;
			}
		}
	}
	&--highlighter-green {
		.mk-fab {
			background-color: lighten(desaturate($highlighter-green, 23.29), 18.82);
			.svg-icon {
				fill: $gray-900;
			}
		}
	}
	&--highlighter-pink {
		.mk-fab {
			background-color: lighten(desaturate($highlighter-pink, 40.63), 29.22);
			.svg-icon {
				fill: $gray-900;
			}
		}
	}
	&--highlighter-purple {
		.mk-fab {
			background-color: lighten(desaturate($highlighter-purple, 23.29), 26.82);
			.svg-icon {
				fill: $gray-900;
			}
		}
	}
}

@include media-breakpoint-up(lg) {
	.main-toolbox-drawer-open .fab-highlight-button {
		position: fixed;
		left: 65%;
	}
}
@include media-breakpoint-up(xl) {
	.main-toolbox-drawer-open .fab-highlight-button {
		position: fixed;
		left: 70%;
	}
}

//floating action menu
.fab-highlight-menu {
	display: block;
	position: fixed;
	left: 50%;
	transform: translateX(-50%);
	width: auto;
	z-index: 5;
	margin-bottom: constant(safe-area-inset-bottom);
	margin-bottom: env(safe-area-inset-bottom);
	/*
	@include desktop {
		transform: translate(-30%);
	}*/
	.mk-fab {
		.svg-icon {
			fill: #444;
			width: 1.5rem;
			height: 1.5rem;
		}
	}

	&__menu {
		bottom: 1px;
		background-color: rgba(255, 255, 255, 0.95);
		border: 1px solid #fff;
		border-radius: 50px;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-around;
		width: 100%;
		box-shadow: $box-shadow-lg;
		border: 5px solid transparent;
		animation: highlighter-fadein 200ms ease-in-out,
			highlight-bounce 650ms 250ms 1;
	}
	.fab-highlight-menu-item {
		padding: 0.5em;
		&__content {
			position: relative;
			border-radius: 50%;
			display: flex;
			align-items: center;
			justify-content: center;
			.mk-fab {
				display: flex;
				align-items: center;
				justify-content: center;
				&.mk-fab--mini {
					width: 46px;
					height: 46px;
				}
				& > .svg-icon {
					opacity: 0;
					fill: #444;
					width: 1.5rem;
					height: 1.5rem;
				}
				border: 1px solid #fff;
				box-shadow: none;
				&:hover {
					.svg-icon {
						fill: #000;
					}
				}
				&:focus {
					outline: $btn-focus-box-shadow;
				}
				&.highlighter-yellow {
					@include background-opacity($highlighter-yellow, 0.4);
					&:hover,
					&:active {
						border: 1px solid rgba($highlighter-yellow, 1);
						@include background-opacity($highlighter-yellow, 0.4);
						transform: scale(1.08);
					}
					&.mk-fab--active {
						@include background-opacity($highlighter-yellow, 0.3);
						border: 1px solid rgba($highlighter-yellow, 1);
						.svg-icon {
							fill: #000;
							opacity: 1;
						}
					}
				}
				&.highlighter-orange {
					@include background-opacity($highlighter-orange, 0.3);
					&:hover {
						border: 1px solid $highlighter-orange;
					}
					&.mk-fab--active {
						@include background-opacity($highlighter-orange, 0.5);
						border: 1px solid $highlighter-orange;
						.svg-icon {
							fill: #000;
							opacity: 1;
						}
					}
				}
				&.highlighter-green {
					@include background-opacity($highlighter-green, 0.4);
					&:hover {
						border: 1px solid rgba($highlighter-green, 1);
					}
					&.mk-fab--active {
						@include background-opacity($highlighter-green, 0.4);
						border: 1px solid rgba($highlighter-green, 1);
						.svg-icon {
							fill: #000;
							opacity: 1;
						}
					}
				}
				&.highlighter-pink {
					@include background-opacity($highlighter-pink, 0.3);
					&:hover {
						border: 1px solid rgba($highlighter-pink, 0.4);
					}
					&.mk-fab--active {
						@include background-opacity($highlighter-pink, 0.3);
						border: 1px solid rgba($highlighter-pink, 0.4);
						.svg-icon {
							fill: #000;
							opacity: 1;
						}
					}
				}
				//purple highligher is only used for Question Stem
				&.highlighter-purple {
					@include background-opacity($highlighter-purple, 0.3);
					border: 1px solid rgba($highlighter-purple, 0.4);
					box-shadow: none;
					margin-right: 0.3em;
					.svg-icon {
						fill: #000;
						opacity: 1;
					}
				}

				&.highlighter-trash {
					background-color: #fff;
					border: 1px solid $highlighter-trash;
					& > .svg-icon {
						opacity: 1;
						fill: $highlighter-trash;
						//width: 1.3rem;
						//height: 1.3rem;
					}
					&:hover,
					&:active {
						@include background-opacity($highlighter-trash, 0.2);
					}
				}
				&.highlighter-info {
					background-color: #fff;
					border: 1px solid $highlighter-info;
					&:hover {
						border: 1px solid $highlighter-info;
					}
					& > .svg-icon {
						fill: $highlighter-info;
						width: 1.3rem;
						height: 1.3rem;
					}
				}
			}
		}
		.fab-notification {
			//display: block !important;
			padding: 0;
			position: absolute;
			left: 25px;
			bottom: -3px;
			width: 23px;
			height: 23px;
			border-radius: 50%;
			display: flex;
			align-items: center;
			justify-content: center;
			border: 1px solid #eee;

			//@include shadow($depth: 1);
			&:hover {
				cursor: pointer;
			}
			&--alert {
				background-color: mksap-color("acp_magenta");
				.svg-icon {
					fill: #fff;
					width: 0.9rem;
					height: 0.9rem;
				}
			}
			&--info {
				//display: block !important;
				background-color: #eee;
				.svg-icon {
					fill: mksap-color("acp_blue_light");
					text-align:center;
					//width: 0.9rem;
					//height: 0.9rem;
				}
			}
		}
	}
}

.highlighter-green {
	&.svg-icon {
		fill: $highlighter-green;
	}
}
.highlighter-yellow {
	&.svg-icon {
		fill: $highlighter-yellow;
	}
}
.highlighter-pink {
	&.svg-icon {
		fill: $highlighter-pink;
	}
}
.highlighter-purple {
	&.svg-icon {
		fill: $highlighter-purple;
	}
}
//empty highlighting icon
.circle.highlighter {
	border-radius: 50%;
	width: 30px;
	height: 30px;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-shrink: 0;
	position: absolute;
	.svg-icon {
		fill: $gray-700;
		//fill:$midnight;
	}
	&:hover,
	&:active,
	&.highlight-active {
		pointer-events: none;
	}
	cursor: default;
	&.highlighter-yellow {
		@include background-opacity($highlighter-yellow, 0.4);
	}
	&.highlighter-purple {
		@include background-opacity($highlighter-purple, 0.4);
	}
}


//****************************************night mode highlighting colors//****************************************//
html.nightMode {
	.highlighter {
		&.highlighter-yellow {
			@include background-opacity($highlighter-yellow-night, 0.25);
			&:hover,
			&:active,
			&.highlight-active {
				@include background-opacity($highlighter-yellow-night, 0.6);
			}
		}
		&.highlighter-orange {
			@include background-opacity($highlighter-orange-night, 0.3);
			&:hover,
			&:active,
			&.highlight-active {
				@include background-opacity($highlighter-orange-night, 0.5);
			}
		}
		&.highlighter-green {
			@include background-opacity($highlighter-green-night, 0.25);
			&:hover,
			&:active,
			&.highlight-active {
				@include background-opacity($highlighter-green-night, 0.6);
			}
		}
		&.highlighter-pink {
			@include background-opacity($highlighter-pink-night, 0.18);
			&:hover,
			&:active,
			&.highlight-active {
				@include background-opacity($highlighter-pink-night, 0.5);
			}
		}
		&.highlighter-purple {
			@include background-opacity($highlighter-purple-night, 0.18);
			&:hover,
			&:active,
			&.highlight-active {
				@include background-opacity($highlighter-purple-night, 0.4);
			}
		}
	}
	.fab-highlight-button {
		&--highlighter-yellow {
			.mk-fab {
				//@include background-opacity($highlighter-yellow-night, 0.6);
				background-color: lighten(
					desaturate($highlighter-yellow-night, 28.29),
					28.82
				);
			}
		}
		&--highlighter-orange {
			.mk-fab {
				//@include background-opacity($highlighter-orange-night, 0.4);
				background-color: lighten(
					desaturate($highlighter-orange-night, 28.29),
					24.82
				);
			}
		}
		&--highlighter-green {
			.mk-fab {
				//@include background-opacity($highlighter-green-night, 0.5);
				background-color: lighten(
					desaturate($highlighter-green-night, 23.29),
					18.82
				);
			}
		}
		&--highlighter-pink {
			.mk-fab {
				//@include background-opacity($highlighter-pink-night, 0.7);
				background-color: lighten(
					desaturate($highlighter-pink-night, 20.63),
					19.22
				);
			}
		}
		&--highlighter-purple {
			.mk-fab {
				//@include background-opacity($highlighter-purple-night, 0.5);
				background-color: lighten(
					desaturate($highlighter-purple-night, 23.29),
					26.82
				);
			}
		}
		&--disabled {
			.mk-fab {
				background-color: $gray-500;
				.svg-icon {
					fill: $gray-800;
				}
			}

			.fab-notification {
				border: 1px solid #eee;
				&--alert {
					background-color: mksap-color("acp_magenta");
					.svg-icon {
						fill: #fff;
					}
				}
				&--info {
					background-color: #eee;
					.svg-icon {
						fill: mksap-color("acp_blue_light");
					}
				}
			}
		}
	}
}
@keyframes highlighter-fadein {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
@keyframes highlighter-fadeout {
	0% {
		opacity: 1;
	}
	100% {
		opacity: 0;
	}
}
@keyframes highlight-bounce {
	0%,
	60%,
	100% {
		transform: translateY(0);
	}
	40% {
		transform: translateY(-8px);
	}
	80% {
		transform: translateY(-3px);
	}
}
