.subspecialty-cards.row {
	.img-thumbnail {
		max-width: 6rem;
	}
}
.topic-cards.card-columns {
	column-count: 1;
}
.topic-cards .complete.card {
	background-color: var(--card-background, $gray-100);
	.card-header {
		background-color: var(--card-background, $gray-100);
	}
	.list-group-item {
		background-color: var(--list-group-bg, $gray-100);
	}
}
.h-60 {
	height: 60px;
}

.edit-learning-plan-item {
	&.newly-checked {
		background-color: mksap-color-alpha("success", 0.1);
	}
	&.newly-unchecked {
		background-color: mksap-color-alpha("danger", 0.1);
	}
}
.accordion.expansion-card {
	.section.sect2 > a.list-group-item {
		.svg-icon {
			margin-left: 1rem;
		}
	}
	.section.sect3 .list-group {
		border-top: none;
	}
	.section-header {
		font-size: 1.7rem;
		font-weight: 600;
		color: $text-color;
		padding: $list-group-item-padding-y;
		margin: 0;
	}
	.list-group-item.active,
	.list-group-item-action {
		//background-color:transparent;
		color: $text-color;
		border-color: 1px solid rgba(0, 0, 0, 0.125);
	}
	.topic-header .btn-link:not(:disabled):not(.disabled):focus,
	.topic-header .btn:not(:disabled):not(.disabled):active,
	.btn:not(:disabled):not(.disabled).active,
	.topic-header .btn-link:hover {
		box-shadow: none;
		border: none;
	}
}

.btn .text-question-theme + span {
	color: mksap-color("question-theme");
}

.accordion-wrapper {
	.bubble-list {
		.list-group-item {
			border: none;
			padding: 0.5rem 1rem;
			&:hover {
				cursor: pointer;
				background-color: $list-group-hover-bg;
			}
		}
	}
}

