// The notes menu that is shown (on larger screens) when clicking the button in the app bar
// Note that on screen sizes smaller than tablet, the notes are shown in a dialog, instead.
.notes-menu {
	display: none;
	background-color: #fff;
	position: fixed;
	top: $top-app-bar-height;
	right: 0;
	width: 400px;
	max-height: calc(100% - #{$top-app-bar-height});
	overflow-y: auto;
	z-index: 3;

	&.notes-menu--show {
		display: block;
	}
	form {
		padding: 1em;
	}
	.note-footer {
		padding: 1em;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: flex-end;
	}
}


.note, .highlight{
	&.note__wrapper, &.highlight__wrapper{
		display:flex;
		flex-flow:row wrap;
		align-items:center;
		justify-content: space-between;
		width:100%;
	}
	&__date {
		display:flex;
		flex-flow:row nowrap;
		align-items:center;
		justify-content: flex-start;
	}
	&__action {
		align-self:flex-end;
	}
}


.tab-pane {
	.notes-form {
		align-items:center;
		width:100%;
		padding:2rem 1rem 0;
	}
	.notes {
		margin-top: 1em;
	}
	.note-footer {
		display: flex;
		justify-content: flex-end;
		width: 100%;
		padding: 1rem;
	}
}


.section-notes{
	display:flex;
	flex-flow:column nowrap;
	align-items:flex-start;
	justify-content:flex-start;
	padding:1rem 0;
	&:not(:last-child) {
		border-bottom:1px solid $gray-400;
	}

}
.subsection-heading {
	@include font-size($h6-font-size);
	font-weight: $font-weight-bold;
}
