// Build the color properties for an alert, to be used by .alert-* classes
@function alert-colors($color) {
	@return (
		"alert-color": color-level($color, $alert-color-level),
		"alert-background": color-level($color, $alert-bg-level),
		"alert-border": color-level($color, $alert-border-level)
	);
}

.alert {
	display: flex;
	flex-flow: row wrap;
	align-items: center;
	justify-content: space-between;
	// @include alert-variant(--alert-background, --alert-border, --alert-color);

	color: var(--alert-color);
	@include gradient-bg(var(--alert-background));
	border-color: var(--alert-border);

	hr {
		// TODO: Maybe use "filter:" property?
		// border-top-color: darken($border, 5%);
		border-top-color: var(--alert-border);
	}
	.btn.btn-icon {
		padding: .75rem;
		.svg-icon {
			width: 1.4em;
			height: 1.4em;
		}
	}

	.alert-link {
		// color: darken($color, 10%);
		color: var(--alert-color);
		filter: brightness(90%);
	}
}

@mixin mksap-alert-variant(
	$color-name,
	$base-color: mksap-color-raw($color-name, $mode: "day"),
	$night-base-color: mksap-color-raw($color-name, $mode: "night"),
	$alert-colors: alert-colors($base-color),
	$night-alert-colors: alert-colors($night-base-color)
) {
	@include define-component-variant(
		"alert",
		$color-name,
		$alert-colors,
		$night-alert-colors
	);
}

@each $color in $mksap-theme-color-names {
	.alert-#{$color} {
		@include mksap-alert-variant($color);
	}
}
