//reference form dialog
.reference {
	display: flex;
	flex-flow: column nowrap;
	.jump-to-content {
		display: flex;
		justify-content: flex-start;
		align-items: center;
		padding: 0.5em 1.5em;
		color: #fff;
		cursor: pointer;
		background-color: mksap-color-alpha("acp_blue_mid", 0.3);
		color: mksap-color("acp_blue_mid");
		.svg-icon {
			margin-left: 10px;
		}
		&:hover {
			background-color: mksap-color("acp_blue_mid");
			color: #fff;
			text-decoration: none;
		}
	}
}
.reference-form {
	padding: 1em;
	/*
	@include handheld-max {
		padding: 1em 0;
	}*/
	.heading {
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: flex-start;
		margin: 1em 0 0;
		.title {
			margin: 0;
			color: $gray-700;
			font-size: 130%;
		}
	}
	form {
		.search-field-item {
			flex: 1;
			height: 100%;
			margin: 0 1em;
			width: 100%;
			/*
			@include handheld-max {
				margin: 1em auto;
			}*/
		}
	}
}
.reference-results {
	padding: 1em;
	/*
	@include handheld-max {
		padding: 1em 0;
	}*/
	.reference-results__title {
		color: $text-color;
	}
}

//inline reference links
.reference-link {
	display: inline;
	//text-decoration: not supported by ie
	align-items: center;
	cursor: pointer;
	span {
		text-decoration: none;
		border-bottom: 1px dotted mksap-color("acp_blue_light");
		padding-bottom: 0.01em;
	}
	&:hover,
	&:active {
		color: mksap-color("acp_blue_light");
	}
	.svg-icon.svg-lab {
		width: 0.85em;
		height: 0.85em;
		margin-left: 0.1em;
		fill: mksap-color("acp_blue_light");
		flex-shrink: 0;
	}
}
