$mksap-theme-colors: ();
$mksap-theme-color-names: ();

@function day-night($day-color, $night-color: null) {
	@if (not $night-color) {
		// A reasonable default night mode color:
		$night-color: scale-color($day-color, $lightness: -2.5%, $saturation: -30%);
	}

	@return (day: $day-color, night: $night-color);
}

@function _assert-day-night-value($val) {
	@if (
		(type-of($val) != "map") or
			(not map-has-key($val, "day")) or
			(not map-has-key($val, "night"))
	) {
		@error "Expected a day-night value. Got: #{inspect($val)}";
	}

	@return null;
}

@mixin add-mksap-color($color-name, $value) {
	@if (type-of($value) == color) {
		$value: day-night($value);
	}
	@if (map-has-key($mksap-theme-colors, $color-name)) {
		@warn "Redefining color #{$color-name}. That's probably a bug.";
	}
	$new-colors: map-merge(
		$mksap-theme-colors,
		(
			$color-name: $value,
		)
	);
	$mksap-theme-colors: $new-colors !global;

	// Add the custom properties for the color to be defined on on :root and .nightMode
	@include _add-color-root-property-definition($color-name, $value);
}

@function color-to-rgb-channels-string($color) {
	@return "#{red($color)}, #{green($color)}, #{blue($color)}";
}

@mixin add-mksap-colors($color-defs) {
	@each $color-name, $value in $color-defs {
		@include add-mksap-color($color-name, $value);
	}
}

// Define theme colors, which will be used to build component variants (btn-danger, etc.)
@mixin add-mksap-theme-colors($color-defs) {
	$mksap-theme-color-names: join(
		$mksap-theme-color-names,
		map-keys($color-defs)
	) !global;
	@include add-mksap-colors($color-defs);
}

// The the css custom property name for a property (e.g. button-background) within a given color
// This should probably not be used outside of _theme_utils.scss
@function _mksap-theme-color-css-prop-name($color-name) {
	@return $color-name;
}

// Returns true if the given color is a page-theme
@function color-is-a-page-theme($color-name) {
	@if index($page-themes, $color-name) {
		@return true;
	} @else {
		@return false;
	}
}

// Read the value of a given color property within a named color.
// Returns something like: var(--info_button-background)
@function mksap-theme-color($color-name) {
	@if (not map-has-key($mksap-theme-colors, $color-name)) {
		@warn "Could not find color: #{$color-name}";
	}
	@return var(--#{_mksap-theme-color-css-prop-name($color-name)});
}
@function mksap-color($color-name) {
	@return mksap-theme-color($color-name);
}

@function mksap-color-alpha($color-name, $alpha) {
	// Add the custom properties containing the RGB for the given color:
	$color-def: mksap-color-definition($color-name);

	$rgb-prop-name: "rgb__#{$color-name}";

	$_: _add-color-root-property-definition(
		$rgb-prop-name,
		day-night(
			color-to-rgb-channels-string(map-get($color-def, "day")),
			color-to-rgb-channels-string(map-get($color-def, "night"))
		),
		$warn-on-redefine: false
	);

	@return rgba(var(--#{$rgb-prop-name}), $alpha);
}

@function mksap-color-definition($color-name) {
	$res: map-get($mksap-theme-colors, $color-name);

	@if (not $res) {
		@warn "Could not find color: #{$color-name}";
	}

	@return $res;
}

// Get the raw (hex, or whatever) value of a theme color.
@function mksap-color-raw($color-name, $mode: null) {
	@if (not $mode) {
		$mode: current-color-mode();
	}
	@if ($mode != "night" and $mode != "day") {
		@warn "invalid color mode: #{inspect($mode)}";
	}
	$color-def: map-get($mksap-theme-colors, $color-name);
	@if (not $color-def) {
		@warn "Couldn't find color: #{$color-name}";
	}
	@return map-get($color-def, $mode);
}

@function page-theme-colors($base-color) {
	@return (
		"theme-primary": $base-color,
		"theme-primary-text": color-yiq($base-color)
	);
}

$_mksap-current-color-mode: null;
@function current-color-mode() {
	@if (not $_mksap-current-color-mode) {
		@error "Called current-color-mode outside of vary-with-night-mode";
	}
	@return $_mksap-current-color-mode;
}

@mixin with-color-mode($mode) {
	$_mksap-current-color-mode: $mode !global;
	@content;
	$_mksap-current-color-mode: null !global;
}

// Include a block of code in both day and night mode. By default,
// mksap-color-raw() will return the color appropriate for the given mode.
@mixin vary-with-night-mode {
	@include with-color-mode("day") {
		@content;
	}

	@include with-color-mode("night") {
		.nightMode & {
			@content;
		}
	}
}

// Include a block of code in night mode
@mixin only-in-night-mode {
	@include with-color-mode("night") {
		.nightMode & {
			@content;
		}
	}
}

// set the "primary" color custom props to use the specified theme color
@mixin page-theme(
	$color-name,
	$base-color: mksap-color-raw($color-name, $mode: "day"),
	$night-base-color: mksap-color-raw($color-name, $mode: "night")
) {
	@include define-component-variant(
		"page-theme",
		$color-name,
		$component-colors: page-theme-colors($base-color),
		$night-component-colors: page-theme-colors($night-base-color)
	);
	// --theme-primary: #{mksap-theme-color($color-name)};
	// --theme-primary-text: #{mksap-theme-color($color-name, "base", "text")};
}

@function _custom-prop-name($prefix, $prop-name) {
	@return "#{$prefix}_#{$prop-name}";
}

@mixin _reference-color-props($prefix, $component-colors) {
	@each $prop-name in map-keys($component-colors) {
		--#{$prop-name}: var(--#{_custom-prop-name($prefix, $prop-name)});
	}
}

@mixin define-component-variant(
	$component-name,
	$color-name,
	$component-colors,
	$night-component-colors
) {
	$prop-name-prefix: "#{$color-name}_#{$component-name}";
	@include _reference-color-props($prop-name-prefix, $component-colors);

	@each $prop-name in map-keys($component-colors) {
		@include _add-color-root-property-definition(
			_custom-prop-name($prop-name-prefix, $prop-name),
			day-night(
				map-get($component-colors, $prop-name),
				map-get($night-component-colors, $prop-name)
			)
		);
	}

	@at-root {
		@if (color-is-a-page-theme($color-name)) {
			// Override the custom property values for "primary" components within page theme classes
			.#{$color-name} {
				@each $prop-name in map-keys($component-colors) {
					--#{_custom-prop-name("primary_#{$component-name}", $prop-name)}: var(
						--#{_custom-prop-name($prop-name-prefix, $prop-name)}
					);
				}
			}
		}
	}
}

$_mksap-color-root-properties: ();

@function _add-color-root-property-definition(
	$property-name,
	$property-value,
	$warn-on-redefine: true
) {
	$prop-defs: $_mksap-color-root-properties;

	// Make sure the value property value is a day-night value.
	$_: _assert-day-night-value($property-value);

	$existing-value: map-get($prop-defs, $property-name);

	@if ($existing-value) {
		@if ($warn-on-redefine) {
			@warn "redefining root property:", $property-name;
		} @else if ($existing-value != $property-value) {
			@warn "redefining root property with new value:", $property-name, $property-value, $existing-value;
		}
	}

	$prop-defs: map-merge(
		$prop-defs,
		(
			$property-name: $property-value,
		)
	);

	$_mksap-color-root-properties: $prop-defs !global;

	@return null;
}

@mixin _add-color-root-property-definition($property-name, $property-value) {
	$_: _add-color-root-property-definition($property-name, $property-value);
}

@mixin emit-mksap-color-root-properties {
	:root {
		@each $prop-name, $prop-value in $_mksap-color-root-properties {
			$value: map-get($prop-value, "day");
			--#{$prop-name}: #{$value};
		}
	}

	.nightMode {
		@each $prop-name, $prop-value in $_mksap-color-root-properties {
			$value: map-get($prop-value, "night");
			--#{$prop-name}: #{$value};
		}
	}
}
