// Build the color properties for the emphasizd text utils, to be used by .text-* classes
@function text-emphasis-colors($color) {
	@return (
		"text-emphasis-color": $color,
		"text-emphasis-hover":
			darken($color, $emphasized-link-hover-darken-percentage)
	);
}

@mixin mksap-text-emphasis-variant(
	$parent,
	$color-name,
	$base-color: mksap-color-raw($color-name, $mode: "day"),
	$night-base-color: mksap-color-raw($color-name, $mode: "night"),
	$component-colors: text-emphasis-colors($base-color),
	$night-component-colors: text-emphasis-colors($night-base-color)
) {
	#{$parent} {
		@include define-component-variant(
			"text",
			$color-name,
			$component-colors,
			$night-component-colors
		);
		color: var(--text-emphasis-color) !important;
	}
	@if $emphasized-link-hover-darken-percentage != 0 {
		a#{$parent} {
			@include hover-focus() {
				color: var(--text-emphasis-hover) !important;
			}
		}
	}
}

// Build the color properties for the background utils, to be used by .bg-* classes
@function background-util-colors($color) {
	@return (
		"background-util": $color,
		"background-util-hover": darken($color, 10%)
	);
}

@mixin mksap-bg-variant(
	$parent,
	$color-name,
	$base-color: mksap-color-raw($color-name, $mode: "day"),
	$night-base-color: mksap-color-raw($color-name, $mode: "night"),
	$component-colors: background-util-colors($base-color),
	$night-component-colors: background-util-colors($night-base-color)
) {
	#{$parent} {
		@include define-component-variant(
			"bg",
			$color-name,
			$component-colors,
			$night-component-colors
		);
		background-color: var(--background-util) !important;
	}
	a#{$parent},
	button#{$parent} {
		@include hover-focus() {
			background-color: var(--background-util-hover) !important;
		}
	}
}

@each $color in $mksap-theme-color-names {
	// Add text-* classes.
	@include mksap-text-emphasis-variant(".text-#{$color}", $color);

	// Add bg-* classes.
	@include mksap-bg-variant(".bg-#{$color}", $color);
}
