//flashcard cards
.flashcards {
	display: flex;
	flex-flow: column nowrap;
	align-items: center;
	justify-content: flex-start;
	width: 100%;
	min-height: 7vh;
	&__wrapper {
		display: flex;
		flex-flow: column nowrap;
		align-items: center;
		justify-content: flex-start;
		position: relative;
		overflow-x: hidden;
		width: 100%;
	}
}
.flashcard__wrapper {
	display: flex;
	flex-flow: column nowrap;
	align-items: center;
	justify-content: center;
	position: relative;
	width: 100%;
}
.flashcard {
	width: 100%;
	max-width: 680px;
	min-height: 400px;
	border-radius: 4px;
	display: flex;
	flex-flow: column nowrap;
	justify-content: space-between;
	animation: newCard 400ms ease-in-out;
	&__section--top {
		box-sizing: border-box;
		min-height: 200px;
		display: flex;
		flex-flow: column wrap;
		align-items: center;
		justify-content: center;
		position: relative;
		width: 100%;
		border-bottom: 1px dotted $gray-300;
		.flashcard__question {
			font-size: 1.2em;
			animation: fadeIn 0.2s ease-in-out;
			box-sizing: border-box;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			text-align: center;
			min-width: 90%;
		}
	}
	&__section--bottom {
		box-sizing: border-box;
		min-height: 200px;
		display: flex;
		flex-flow: column wrap;
		align-items: center;
		justify-content: center;
		position: relative;
		width: 100%;

		.flashcard__prompt {
			color: $link-color;
			font-size: 1.2em;
			display: block;
			cursor: pointer;
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			display: flex;
			flex-flow: row nowrap;
			align-items: center;
			justify-content: center;
			border:none;
			background-color:transparent;//$night-mode-surface;
			&:not(:disabled):not(.disabled):focus,
	&:not(:disabled):not(.disabled).focus,
	&:not(:disabled):not(.disabled):visited:focus,
	&:not(:disabled):not(.disabled):visited.focus {
		box-shadow: $input-btn-focus-box-shadow;
	}
			&:focus,
			&:hover,
			&:active {

					color: $link-color;
        			background-color: lighten($acp_blue_lightest, 25%);
					outline:none;


			}
		}
		.flashcard__answer {
			font-size: 1.2em;
			font-weight: 600;
			color: $text-color;
			animation: fadeIn 1s ease-in-out;
			box-sizing: border-box;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			text-align: center;
			min-width: 90%;
		}
	}
}

.flashcard__actions {
	margin-top: 2em;
	width: 100%;
	//animation: fadeIn 1s ease-in-out;
	.flashcard__action--icon-buttons {
		display: none;
		margin: 0 1em;
		width: 100%;
	}
	.flashcard__action--buttons {
		margin: 0;
		width: 100%;
		max-width: 685px;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-around;
		.btn {
			padding: 8px 36px;
			height: 44px;
			@include media-breakpoint-down(sm){
				padding:10px;
				height:auto;
			}
			.svg-icon {
				width: 2em;
				height: 2em;
			}
		}
	}
}

.flashcard__progress {
	display: flex;
	flex-flow: column nowrap;
	align-items: flex-end;
	justify-content: flex-end;
	margin-top: 2em;
	margin-bottom: 1em;
	width: 100%;
	max-width: 680px;
	position: relative;
	.progress__graph.fc {
		position: relative;
		height: 22px;
		margin-top: 0;
		border-radius: 0;
		background-color: transparent;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-around;
		.dot {
			width: 14px;
			height: 14px;
			border: 2px solid #ccc;
			border-radius: 50%;
			background-color: #eee;
			display: flex;
			flex-flow: row nowrap;
			align-items: center;
			justify-content: center;
			z-index: 2;
			.current {
				opacity: 1;
				display: flex;
				width: 10px;
				height: 10px;
				border-radius: 50%;
				background-color: #fff;
				border: 2px solid #fff;
			}
			.correct {
				opacity: 1;
				display: flex;
				width: 10px;
				height: 10px;
				border-radius: 50%;
				background-color: mksap-color("correct");
				border: 2px solid #fff;
			}
			.unsure {
				opacity: 1;
				display: flex;
				width: 10px;
				height: 10px;
				border-radius: 50%;
				background-color: mksap-color("unsure");
				border: 2px solid #fff;
			}
			.incorrect {
				opacity: 1;
				display: flex;
				width: 10px;
				height: 10px;
				border-radius: 50%;
				background-color: mksap-color("wrong");
				border: 2px solid #fff;
			}
			&.complete {
				padding: 2px;
				box-sizing: border-box;
				width: 20px;
				height: 20px;
				.svg-icon {
					fill: $slate;
					width: 100%;
					height: 100%;
				}
			}
		}
		.bar {
			border: 2px solid #ccc;
			width: 92%;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}

		.progress__bar {
			.progress__bar--correct,
			.progress__bar--incorrect,
			.progress__bar--unsure {
				margin: 0;
				animation: fadeIn 300ms ease-in;
			}
		}
	}
	.complete-goal {
		position: absolute;
		right: 0;
		height: 21px;
		top: 0;
		text-align: right;
		.label {
			margin-top: 25px;
			margin-right: 15px;
			font-size: 90%;
		}
	}
}

@keyframes fadeIn {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
@keyframes newCard {
	0% {
		opacity: 0;
		transform: translateY(2rem);
		box-shadow: none;
	}
	100% {
		opacity: 1;
		transform: translateY(0);
	}
}

//details and checkins

	.flashcards .answered {
		min-width: 110px;
		display: flex;
		flex-flow:row nowrap;
		align-items:center;
		justify-content:flex-start;
		.svg-icon {
			margin-right:1rem;
		}
		&.correct {
			color: mksap-color("correct");
		}
		&.wrong {
			color: mksap-color("wrong");
		}
		&.unsure {
			color: mksap-color("unsure");
		}
	}

.flashcard.checkin {
	justify-content: flex-start;
		.circle {
			background-color: #ddd;
			border-radius: 50%;
			width: 25px;
			height: 25px;
			padding: 4px;
			display: flex;
			flex-flow: row nowrap;
			align-items: center;
			justify-content: center;
			box-sizing: border-box;
			.svg-icon {
				fill: #3f5366;
				width: 100%;
				height: 100%;
			}
		}
		.title {
			padding-top: 1rem;
			box-sizing: border-box;
			text-align: center;
		}
	.flashcard__section {
		display: flex;
		flex-flow: column nowrap;
		align-items: center;
		width: 100%;
		padding: 0 2em 2em;
		border-bottom: 1px solid #ccc;
		box-sizing: border-box;
		&--results {
			display: flex;
			flex-flow: row wrap;
			width: 100%;
			align-items: center;
			justify-content: space-around;
			padding: 1.5em 1em;
			box-sizing: border-box;
			/*
			@include phone-max {
				flex-flow: column nowrap;
				align-items: flex-start;
			}*/
		}
		label {
			display: flex;
			flex-flow: row wrap;
			align-items: center;
			justify-content: space-between;
			width: 100%;
		}
		.progress {
			width: 100%;
		}
	}
	.flashcard__graph {
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-around;
		:not(nav).mk-list .mk-list-item {
			border-bottom: none;
		}
	}
	.flashcard__actions {
		margin: 0;
		padding: 1em 0 2em;
		display: flex;
		flex-flow: column nowrap;
		align-items: center;
		border: 0;
		.btn {
			display: block;
			width: 100%;
		}
	}
}

.flashcard__action--button {
	display: flex;
	flex-flow: column nowrap;
	align-items: center;
	.btn {
		margin-top: 0.5em;
	}
}

//flashcard answersheet details
.flashcard__details
	:not(nav).mk-list.multi-line-list
	.mk-list-item
	.multi-line-item {
	div {
		padding: 0.5em 0;
		display: flex;
		flex-flow: row nowrap;
		align-items: flex-start;
		justify-content: flex-start;
		height: 100%;
	}
}
@media screen and (max-width: 1199px) {
	.flashcard__details :not(nav).mk-list.multi-line-list .mk-list-item {
		display: flex;
		flex-flow: row wrap;
		align-items: center;
		justify-content: space-between;
	}
}
.flashcards .list-group {
	.response-item {
		min-width:11rem;
		display:flex;
		flex-flow: row wrap;
		align-items:center;
		justify-content:flex-start;
	}
}

