.empty-message {
	border: 2px dashed $gray-200;
	display:flex;
	flex-flow:row wrap;
	align-items: center;
	justify-content:flex-start;
	padding:1rem;
	margin: 0 0 1rem;
}

.tutorial {
	display: flex;
	flex-flow: column nowrap;
	align-items: flex-start;
	justify-content: space-around;
	align-content: space-between;
	padding: 1em;
	box-sizing: border-box;
	flex: 0 0 auto;
	/*
	@include handheld-max {
		padding: 0.5em;
	}*/
	.heading {
		padding: 1em;
		.subtitle {
			font-size: 0.9em;
		}
	}
	&__item {
		box-sizing: border-box;
		padding: 1em;
		display: flex;
		flex-flow: row nowrap;
		align-items: flex-start;
		width: 100%;
		line-height: 1.6em;
	}
	&__graphic {
		margin-right: 16px;
	}
	.tutorial__blockquote {
		display: flex;
		flex-flow: row wrap;
		align-items: flex-start;
		justify-content: space-between;
		align-content: space-between;
		box-sizing: border-box;
		padding: 1em 2em;
		margin: 0 auto;
		width: 100%;
		flex: auto;
		.tutorial__options {
			display: flex;
			flex-flow: column wrap;
			align-items: flex-start;
			justify-content: flex-start;
			margin: 0 1.5em;
			width: 40%;
			text-align: left;
			.tutorial__text {
				margin: 1em auto;
				width: 100%;
			}
		}
		.tutorial__example {
			display: table;
			//flex-flow: column wrap;
			align-items: center;
			//justify-content: flex-start;
			//margin: 0 1em;
			width: 30%;
			text-align: center;
			.svg-icon {
				&.incorrect {
					fill: #dc291e;
				}
				&.unsure {
					fill: #f1a403;
				}
				&.correct {
					fill: #2eb135;
				}
			}
			caption {
				font-size: 90%;
			}
		}
		/*
		@include handheld-max {
			flex-flow: column nowrap;
			.tutorial__example {
				width: 100%;
				padding: 0.5em 0;
				margin: 0;
				text-align: left;
				.svg-icon {
					margin-right: 1em;
				}
			}
			.tutorial__options {
				width: 100%;
				flex-flow: column nowrap;
				align-items: flex-start;
				justify-content: flex-start;
				align-content: flex-start;
				padding: 1.5em 0;
				margin: 0;
				text-align: left;
			}
		}*/
	}
}
.marketing {
	img {
		width: 100%;
	}

	.promo.books {
		.mk-card.book {
			.book--content {
				display: flex;
				flex-flow: column nowrap;
				flex: 1 1 auto;
			}
		}
	}
}
.cta {
	text-align: center;
	padding-top: 1em;
	padding-bottom: 2em;
	margin-bottom: 2.5em;
}
