//button colors
// stylelint-disable selector-no-qualifying-type

//
// Base styles
//

// We frequently want white for the foreground color of buttons instead of $yiq-text-light.
// This funciton is an easier way to do that.
@function button-color-yiq($color, $dark: $yiq-text-dark, $light: $white) {
	@return color-yiq($color, $dark: $dark, $light: $light);
}

// Build the color properties for a button, to be used by .btn-* classes
@function button-colors(
	$background,
	$border: $background,
	$hover-background: darken($background, 7.5%),
	$hover-border: darken($border, 10%),
	$active-background: darken($background, 10%),
	$active-border: darken($border, 12.5%),
	$disabled-color: $white,
	$disabled-border: $gray-400,
	$disabled-background: rgba($background, .5)
) {
	@return (
		"button-color": button-color-yiq($background),
		"button-background": $background,
		"button-border": $border,
		// hover states:
		"button-hover-background": $hover-background,
		"button-hover-color": button-color-yiq($hover-background),
		"button-hover-border": $hover-border,
		// active states:
		"button-active-background": $active-background,
		"button-active-color": color-yiq($active-background),
		"button-active-border": $active-border,
		// disabled
		"button-disabled-color": #{$disabled-color},
		"button-disabled-border": #{$disabled-border},
		"button-disabled-background": #{$disabled-background}
	);
}

// Build the color properties for an outline button, to be used by .btn-outline-* classes
@function outline-button-colors(
	$color,
	$hover-color: darken($color, 10%),
	$hover-background: rgba($color, 0.85),
	$active-background: $color,
	$active-border: $color,
	$disabled-color: rgba($white, 1),
	$disabled-border: rgba($gray-400, .2),
	$disabled-background: rgba($color, .5)
) {
	@return (
		"button-color": #{$color},
		"button-background": #{button-color-yiq($color)},
		"button-border": #{$color},
		// hover states:
		"button-hover-background": #{$hover-background},
		"button-hover-color": #{button-color-yiq($hover-color)},
		"button-hover-border": #{$hover-color},
		// active states:
		"button-active-background": #{$active-background},
		"button-active-color": #{button-color-yiq($active-background)},
		"button-active-border": #{$active-border},
		//disabled
		"button-disabled-color": #{$disabled-color},
		"button-disabled-border": #{$disabled-border},
		"button-disabled-background": #{$disabled-background}
	);
}

@mixin mksap-button-variant(
	$color-name,
	$base-color: mksap-color-raw($color-name, $mode: "day"),
	$night-base-color: mksap-color-raw($color-name, $mode: "night"),
	$button-colors: button-colors($base-color),
	$night-button-colors: button-colors($night-base-color)
) {
	@include define-component-variant(
		"btn",
		$color-name,
		$button-colors,
		$night-button-colors
	);
}

@mixin mksap-button-outline-variant(
	$color-name,
	$base-color: mksap-color-raw($color-name, $mode: "day"),
	$night-base-color: mksap-color-raw($color-name, $mode: "night"),
	$button-colors: outline-button-colors($base-color),
	$night-button-colors: outline-button-colors($night-base-color)
) {
	@include define-component-variant(
		"btn-outline",
		$color-name,
		$button-colors,
		$night-button-colors
	);
}

// .btn-foo {
// 	@include mksap-button-variant("blurple");
// }
.btn {
	transition: $btn-transition;
	outline: 0;
	//box-shadow: $btn-box-shadow;//0 0 0.5rem rgba(0, 0, 0, 0.3); /* black with 30% opacity */
	cursor: pointer;
	display: inline-flex;
	flex-flow: row nowrap;
	align-items: center;
	font-family: $btn-font-family;
	font-weight: $btn-font-weight;
	color: $body-color;
	text-align: center;
	vertical-align: middle;
	user-select: none;
	background-color: var(--button-background);
	border: $btn-border-width;
	border-color: var(--button-border, currentcolor);
	border-style: solid;
	white-space: nowrap;
	text-decoration: none;
	//font-size:17px; //don't let font size adjust this size
	@include button-size(
		$btn-padding-y,
		$btn-padding-x,
		$btn-font-size,
		$btn-line-height,
		$btn-border-radius
	);
	.svg-icon {
		flex-shrink: 0;
		height: 1.6rem;
		width: 1.6rem;
		&.svg-icon__first {
			margin-right: 0.5rem;
		}
		&.svg-icon__last {
			margin-left: 0.5rem;
		}
	}
	&:focus,
	&.focus {
		outline: 0;
		//border:$input-focus-border-color;
		box-shadow: $btn-focus-box-shadow;//$input-btn-focus-box-shadow;
	}
}
.btn {
	background-color: var(--button-background);
	color: var(--button-color);
	&.ripple {
		background-color: var(--button-background);
		background-position: center;
		transition: background 0.6s;
	}
	&:not(:disabled):not(.disabled):focus,
	&:not(:disabled):not(.disabled).focus,
	&:not(:disabled):not(.disabled):visited:focus,
	&:not(:disabled):not(.disabled):visited.focus {
		box-shadow: $input-btn-focus-box-shadow;
	}
	&:disabled {
		//background-color: var(--button-disabled-background, currentcolor);
		//color: var(--button-disabled-color);
		//border-color: var(--button-disabled-border);
	}
	&:hover {
		background: var(--button-hover-background);
		color: var(--button-hover-color);
		text-decoration: none;
		.svg-icon {
			fill: var(--button-hover-color, currentcolor);
		}
		box-shadow: none;
	}
	&:not(:disabled):not(.disabled):active {
		background: var(--button-hover-background);
		color: val(--button-active-color, currentcolor);
		background-size: 100%;
		transition: background 0s;
		.svg-icon {
			fill: val(--button-active-color, currentcolor);
		}
	}
}
// Future-proof disabling of clicks on `<a>` elements
a.btn.disabled,
fieldset:disabled a.btn,
button:disabled {
	pointer-events: none;
	box-shadow: none;
	background-color: var(--button-disabled-background);
	color: var(--button-disabled-color);
	border-color: var(--button-disabled-border);
}



//
// Alternate buttons
//

.btn-group-toggle.btn-group {
	.btn {
		border-radius: $btn-border-radius-lg;
		font-size: 1.3rem;
		padding: 0.275rem 1.3rem;
		&:not(:last-child):not(.dropdown-toggle),
		.btn-group > .btn-group:not(:last-child) > .btn {
			border-top-right-radius: 0;
			border-bottom-right-radius: 0;
		}
		&:not(:first-child),
		.btn-group > .btn-group:not(:first-child) > .btn {
			border-top-left-radius: 0;
			border-bottom-left-radius: 0;
		}
	}

	.btn-primary:not(:disabled):not(.disabled) {
		border: 1px solid $gray-600;
		background-color: $white;
		color: $gray-600;
		.svg-icon {
			fill: $gray-600;
			width: 1.3rem;
			height: 1.3rem;
		}
		&:hover {
			background-color: rgba(0, 52, 151, 0.3);
			color: rgba(0, 52, 151, 1);
		}
	}

	.btn-outline-primary:not(:disabled):not(.disabled):active {
		background-color: mksap-color("white");
		color: rgba(0, 52, 151, 1);
	}
	.btn-primary:not(:disabled):not(.disabled).active {
		background-color: rgba(0, 52, 151, 1);
		color: white;
		.svg-icon {
			fill: $white;
		}
	}
}
//round fab button
//floating action button
.mk-fab {
	width: $fab-size;
	height: $fab-size;
	border-radius: 50%;
	border: none;
	box-shadow: $box-shadow-lg;
	position:relative;
	&:hover {
		transform: scale(1.08);
	}
	&:focus,
	&.focus,
	&:focus-visible {
		outline: 0;
		box-shadow: $btn-focus-box-shadow;
	}
	.svg-icon {
		position: absolute;
		top:50%;
		left:50%;
		transform:translate(-50%, -50%);
		//margin: -8.5px 0 0 -8.5px;
	}
}
//stacked group buttons toggle,dyl
.btn-group-vertical {
	.btn {
		border-radius: 0;
	}
	label:not(:disabled) {
		padding-left: 3rem;
	}
	input[type="radio"]:checked:not(:disabled):before,
	label.active:not(:disabled):before {
		content: "\2713 ";
		margin-right: 1.2rem;
		margin-left: -2rem;
		color: mksap-color("correct");
	}
}

// Button variants

@each $color in $mksap-theme-color-names {
	.btn-#{$color} {
		@include mksap-button-variant($color);
	}

	.btn-outline-#{$color} {
		@include mksap-button-outline-variant($color);
	}
}

//
// Link buttons
//

// Make a button look and behave like a link

.btn.btn-link.ripple,
.btn-outline-link,
.btn-link {
	display: inline-block;
	font-weight: $font-weight-normal;
	color: $link-color;
	text-decoration: $link-decoration;
	//font-size: $font-size-base;
	font-size: 17px; //don't let font size adjust this
	line-height: $line-height-base;
	background-color: transparent;
	border: none;
	//white-space: pre-wrap;
	overflow-wrap: break-word;
	word-wrap: break-word;
	word-break: break-word;
	padding: 0;
	vertical-align: baseline;
	font-family: $font-family-base;
	text-align: left;

	&:not(:disabled):not(.disabled):focus,
	&:not(:disabled):not(.disabled).focus,
	&:not(:disabled):not(.disabled):visited:focus,
	&:not(:disabled):not(.disabled):visited.focus {
		text-decoration: none;
		border-radius:0;
		//box-shadow:none;
		//border-bottom: 1px dotted #80bdff;
	}
	@include hover {
		color: $link-hover-color;
		text-decoration: $link-hover-decoration;
		box-shadow: none;
		background-color: transparent;
		.svg-icon {
			fill: $link-hover-color;
		}
	}
	&:not(:disabled):not(.disabled):active {
		background-color:transparent;
	}
	&:disabled,
	&.disabled {
		color: $btn-link-disabled-color !important;
		pointer-events: none;
		background-color: transparent;
	}
button.btn-link {
	white-space: nowrap;
}


	// No need for an active state here
}
button.close {
	position:absolute;
	right:2rem;
	padding:1rem 2rem;
	margin:-1rem -2rem -1rem auto;
	&:not(:disabled):not(.disabled):focus,
	&:not(:disabled):not(.disabled).focus,
	&:not(:disabled):not(.disabled):visited:focus,
	&:not(:disabled):not(.disabled):visited.focus {
		outline:0;
		border-radius:$card-border-radius;
		box-shadow: $input-btn-focus-box-shadow;

	}
	&:not(:disabled):not(.disabled):hover,
	&:not(:disabled):not(.disabled):focus {
	color:$link-color;
	}

}

.btn.btn-icon {
	display: inline-flex;
	flex-flow: row nowrap;
	align-items: center;
	justify-content: center;
	text-align: center;
	line-height: 2rem;
	padding: 1rem;
	border-radius: 50%;
	.svg-icon {
		width: 1.5em;
		height: 1.5em;
	}
	&.btn-niu {
		background-color: transparent;
		border: none;
		.svg-icon {
			fill:var(--theme-primary);
		}
		&:hover, &:active {
			background-color: transparent;
			border: none;
			.svg-icon {
				fill: var(--button-hover-background);
			}
		}
	}
	&.question-star-toggle {
		width: 4rem;
		height: 4rem;
		background-color: mksap-color("white");
		color: $link-color;
		border-color: 1px solid $link-color;
		.svg-icon {
			fill: $gray-500;
		}
		.btn-primary.ripple:not(:disabled):not(.disabled):focus,
		&:not(:disabled):not(.disabled):focus,
		&:not(:disabled):not(.disabled).focus,
		&:not(:disabled):not(.disabled):visited:focus,
		&:not(:disabled):not(.disabled):visited.focus {
			box-shadow: $input-btn-focus-box-shadow;
		}
		&:hover,
		&:not(:disabled):not(.disabled):active {
			background: mksap-color("white");
			//background: rgba($new, .3) radial-gradient(circle, transparent 1%, lighten($white, 10%) 1%) center/15000%;
			color: mksap-color("acp_gold");
			.svg-icon {
				fill: mksap-color("acp_gold");
			}
			box-shadow: none;
		}

		&--starred {
			background-color: white;
			border-color: mksap-color("acp_gold");
			color: mksap-color("acp_gold");
			.svg-icon {
				fill: mksap-color("acp_gold");
				//fill: mksap-color("acp_gold");
			}
		}
	}
}
.btn-sm {
	padding: $btn-padding-y-sm $btn-padding-x-sm;
	font-size: 1.3rem;
	line-height: $btn-line-height-sm;
}
//
// Block button
//

.btn-block {
	display: block;
	width: 100%;
	border-radius: $btn-border-radius;

	// Vertically space out multiple block buttons
	+ .btn-block {
		margin-top: $btn-block-spacing-y;
	}
}

// Specificity overrides
input[type="submit"],
input[type="reset"],
input[type="button"] {
	&.btn-block {
		width: 100%;
	}
}

.button-spinner {
	animation: button-spinner-rotate 2s linear infinite;
	transform-origin: center center;

	& .button-spinner__path {
		stroke-dasharray: 1, 200;
		stroke-dashoffset: 0;
		stroke: $white;
		animation: button-spinner-dash 1.5s ease-in-out infinite;
		stroke-linecap: round;
	}
}

@keyframes button-spinner-rotate {
	100% {
		transform: rotate(360deg);
	}
}

@keyframes button-spinner-dash {
	10% {
		stroke-dasharray: 1, 200;
		stroke-dashoffset: 0;
	}
	50% {
		stroke-dasharray: 89, 200;
		stroke-dashoffset: -35px;
	}
	100% {
		stroke-dasharray: 89, 200;
		stroke-dashoffset: -124px;
	}
}

//stretchedlink styles
a.stretched-link {
	white-space: nowrap;
	text-decoration: none;
	&:not(:disabled):not(.disabled):focus,
	&:not(:disabled):not(.disabled).focus,
	&:not(:disabled):not(.disabled):visited:focus,
	&:not(:disabled):not(.disabled):visited.focus {
		outline:0;
		border-radius:$btn-border-radius;
		box-shadow: $input-btn-focus-box-shadow;
	}
	&:hover {
		text-decoration: none;
		cursor: pointer;
		&:after {
			background-color: (rgba(255, 255, 255, 0.1));
			box-shadow: $box-shadow;
			border-radius: $border-radius-lg;
		}
	}
}
