//Snackbars contain a single line of text directly related to the operation performed
.drawer-collapsed .snackbar {
	left: calc($navDrawer-width + 50%);
}
.main-toolbox-drawer-open .snackbar {
	left: calc($toolbox-right-drawer-width + 50%);
}
.snackbar {
	display: flex;
	position: fixed;
	bottom: 5em;
	left: 50%;
	flex-flow: row wrap;
	align-items: center;
	justify-content: space-between;
	padding: 1rem 1.5rem;
	opacity: 0;
	visibility: hidden;
	background-color: $nav-bar-bg-color;
	font-size: 1.4rem;
	text-align:center;
	color: white;
	background-clip: padding-box;
	box-shadow: $box-shadow-sm;
	border-radius: $border-radius-lg;
	width: 360px;
	margin-left: -180px;
	max-width: 568px;
	z-index: $zindex-popover;
	.btn.btn-link {
		color: mksap-color("acp_blue_light");
	}

	//&[aria-hidden="true"] {}
	&[aria-hidden="false"] {
		opacity: 1;
		visibility: visible;
		-webkit-animation: fadein 0.5s, fadeout 0.6s 3.7s;
		animation: fadein 0.5s, fadeout 0.6s 3.7s;
	}
}
@-webkit-keyframes fadein {
	from {
		bottom: 0;
		opacity: 0;
	}
	to {
		bottom: 5em;
		opacity: 1;
	}
}

@keyframes fadein {
	from {
		bottom: 0;
		opacity: 0;
	}
	to {
		bottom: 5em;
		opacity: 1;
	}
}

@-webkit-keyframes fadeout {
	from {
		bottom: 5em;
		opacity: 1;
	}
	to {
		bottom: 0;
		opacity: 0;
	}
}

@keyframes fadeout {
	from {
		bottom: 5em;
		opacity: 1;
	}
	to {
		bottom: 0;
		opacity: 0;
	}
}
