//old global variables
@import "../bootstrap/functions";
@import "../bootstrap/variables";
@import "../bootstrap/mixins";

// TRANSITION TIMING
$tFast: 0.1s;
$tMed: 0.25s;
$tSlow: 0.75s;

//$max-text-width: $line-height * 33;//
//$txt-size: 2vmin;

//new
$toolbox-right-drawer-width: 300px;//30rem;
$navDrawer-width: 220px;//22rem; //240px;
$navDrawer-collapse: 60px;//6rem; //60px;
$top-app-bar-height: 6rem;
$nav-bar-bg-color: $gray-900;//#231f20;
$pagination-height: 6rem;
$reading-width: 82rem; //76.8rem;
$container-padding-xl: calc(100vw/1.76 + #{$reading-width / 2});
$container-padding-lg: calc(100vw/1.6 + #{$reading-width / 2});
//$container-padding-md: calc(100vw/1.5 + #{$reading-width / 2});
// Variables
//
// Variables should follow the `$component-state-property-size` formula for
// consistent naming. Ex: $nav-link-disabled-color and $modal-content-box-shadow-xs.

// Color system

$white: #fff !default;
$gray-100: #f0f0f0;
$gray-200: #e0e0e0;
$gray-300: #d0d0d0;
$gray-400: #b8b8b8;
$gray-500: #787878;
$gray-600: #606060;
$gray-700: #484848;
$gray-800: #303030;
$gray-900: #181818;
$black: #000 !default;

$grays: ();
// stylelint-disable-next-line scss/dollar-variable-default
$grays: map-merge(
	(
		"100": $gray-100,
		"200": $gray-200,
		"300": $gray-300,
		"400": $gray-400,
		"500": $gray-500,
		"600": $gray-600,
		"700": $gray-700,
		"800": $gray-800,
		"900": $gray-900,
	),
	$grays
);
$correct: #238627;//#2ba631;
$wrong: #D42525;//#db3333;
$warning: #ff7700;
$unsure: #A06B08;//#ba8d1c; //#daa520;

$acp_green: #007e66; //text-theme
$acp_green_mid: #2eb135;
$acp_green_bright: #91c83c;
$acp_green_light: #d7e241;

$acp_blue_dark:#003497; //question-theme
$acp_blue_mid: #0063be; //link blue
$acp_blue_light: #007CAD;//#00a0df; //info
$acp_blue_lightest: #7cd0f3; //hospitalist

$acp_red: #dc291e; //incorrect
$acp_magenta: #d21146; //flashcards

$acp_orange: #e66b00; //#e66b00; //warning
$acp_yellow: #ffe000; //unsure
$acp_gold: #9D8319;//#eeb825;
$manilla: #fef9ec;

$acp_violet: #702785; //quiz theme
$acp_violet_mid: #95519e;

$acp_brown: #61361d;
$acp_brown_mid: #B35E0F;//#bf650f;
$acp_brown_light: #cea98d;

$slate: #3f5366; //primary and dashboard-theme

$highlighter-yellow: #efdd00; //#ebef00;
$highlighter-orange: #ffa03e;
$highlighter-green: #4ce62a;
$highlighter-pink: #ff2a9d;
$highlighter-purple: #bc13fe;
$highlighter-trash: #c8291e;
$highlighter-info: #00a0df;

$night-mode-bg:#121212;
$night-mode-surface:#282828;
$night-mode-color:#efefef;
$night-button-colors: rgba($black, .65);

$colors: () !default;

// Set a specific jump point for requesting color jumps
$theme-color-interval: 8% !default;

// The yiq lightness value that determines when the lightness of color changes from "dark" to "light". Acceptable values are between 0 and 255.
$yiq-contrasted-threshold: 150;

// Customize the light and dark text colors for use in our YIQ color contrast function.
$yiq-text-dark: #121212;
$yiq-text-light: #fff;

// Options
//
// Quickly modify global styling by enabling or disabling optional features.

$enable-caret: true;
$enable-rounded: true;
$enable-shadows: true;
$enable-gradients: false !default;
$enable-transitions: true;
$enable-prefers-reduced-motion-media-query: true;
$enable-hover-media-query: false !default; // Deprecated, no longer affects any compiled CSS
$enable-grid-classes: true;
$enable-pointer-cursor-for-buttons: true;
$enable-print-styles: true;
$enable-responsive-font-sizes: true;
$enable-validation-icons: true;
$enable-deprecation-messages: true;

// Spacing
//
// Control the default styling of most Bootstrap elements by modifying these
// variables. Mostly focused on spacing.
// You can add more entries to the $spacers map, should you need more variation.

$spacer: 1.6rem;
$spacers: () !default;
// stylelint-disable-next-line scss/dollar-variable-default
$spacers: map-merge(
	(
		0: 0,
		1: (
			$spacer * 0.25,
		),
		2: (
			$spacer * 0.5,
		),
		3: $spacer,
		4: (
			$spacer * 1.5,
		),
		5: (
			$spacer * 3,
		),
	),
	$spacers
);

// This variable affects the `.h-*` and `.w-*` classes.
$sizes: () !default;
// stylelint-disable-next-line scss/dollar-variable-default
$sizes: map-merge(
	(
		25: 25%,
		50: 50%,
		75: 75%,
		100: 100%,
		auto: auto,
	),
	$sizes
);

// Body
//
// Settings for the `<body>` element.

$body-bg: #efefef;
$body-color: #181d23;//#323537;
$text-color: var(--text-color, #181d23);
$link-color: var(--link-color, #0063be);
$link-hover-color: $link-hover-color;
$link-hover-decoration: underline;
$link-decoration: none;
$link-visited-color: $link-hover-color;
$emphasized-link-hover-darken-percentage: 15%;
 a {
	 color:$link-color;
	 text-decoration:$link-decoration;
	 &:hover {
		 text-decoration:$link-hover-decoration;
	 }
 }
// Grid breakpoints
//
// Define the minimum dimensions at which your layout will change,
// adapting to different screen sizes, for use in media queries.

$grid-breakpoints: (
	xs: 0,
	sm: 576px,
	md: 768px,
	lg: 992px,
	xl: 1200px,
	xxl: 1400px,
);

@include _assert-ascending($grid-breakpoints, "$grid-breakpoints");
@include _assert-starts-at-zero($grid-breakpoints, "$grid-breakpoints");

// Grid containers
//
// Define the maximum width of `.co//ntainer` for different screen sizes.

$container-max-widths: (
	sm: 540px,
	md: 740px,
	lg: 960px,
	xl: 1140px,
	xxl: 1320px,
);

//@include _assert-ascending($container-max-widths, "$container-max-widths");

// Grid columns
//
// Set the number of columns and specify the width of the gutters.

$grid-columns: 12;
$grid-gutter-width: 3rem;

// Components
//
// Define common padding and border radius sizes and more.

$line-height-lg: 2;
$line-height-sm: 1;

$border-width: 1px;
$border-color: $gray-300;

$border-radius: 0 !default;
$border-radius-lg: 0.9rem;
$border-radius-sm: 0.4rem;

$rounded-pill: 50rem;

$box-shadow-sm: 0 0.125rem 0.25rem rgba($black, 0.075);
$box-shadow: 0 0.5rem 1rem rgba($black, 0.15);
$box-shadow-lg: 0 1rem 3rem rgba($black, 0.175);

$component-active-color: var(--component-active-color, $text-color);
//$component-active-bg: theme-color("black");
$component-active-bg: var(--component-active-bg, $acp_blue_lightest);

$caret-width: 0.3em !default;
$caret-vertical-align: $caret-width * 0.85 !default;
$caret-spacing: $caret-width * 0.85 !default;

$transition-base: all 0.2s ease-in-out;
$transition-fade: opacity 0.25s linear;
$transition-collapse: height 0.45s ease;

$embed-responsive-aspect-ratios: () !default;
// stylelint-disable-next-line scss/dollar-variable-default
$embed-responsive-aspect-ratios: join(
	((21 9), (16 9), (4 3), (1 1)),
	$embed-responsive-aspect-ratios
);

// Typography
//
// Font, line-height, and color for body text, headings, and more.

// stylelint-disable value-keyword-case
$font-family-sans-serif: "Open Sans", "Segoe UI", Tahoma, sans-serif;
$font-family-monospace: SFMono-Regular, Menlo, Monaco, Consolas,
	"Liberation Mono", "Courier New", monospace !default;
$font-family-base: "Open Sans", "Segoe UI", Tahoma, sans-serif;
// stylelint-enable value-keyword-case
// Base font size

$rfs-base-font-size: 1.7rem;
$rfs-font-size-unit: rem;

// Breakpoint at where font-size starts decreasing if screen width is smaller
$rfs-breakpoint: 1600px;
$rfs-breakpoint-unit: px;

$font-size-base: 1.7rem; // Assumes the browser default, typically `16px`
$font-size-lg: $font-size-base * 1.25;
$font-size-sm: $font-size-base * 0.875;


$font-weight-lighter: lighter;
$font-weight-light: 300;
$font-weight-normal: 400;
$font-weight-bold: 600;
$font-weight-bolder: bolder;

$font-weight-base: $font-weight-normal;
$line-height-base: 1.4;

$h1-font-size: $font-size-base * 2.2;
$h2-font-size: $font-size-base * 1.85;
$h3-font-size: $font-size-base * 1.7;
$h4-font-size: $font-size-base * 1.55;
$h5-font-size: $font-size-base * 1.25;
$h6-font-size: $font-size-base * 1;

$headings-margin-bottom: $spacer / 2;
$headings-font-family: null !default;
$headings-font-weight: 500;
$headings-line-height: $line-height-base; //1.667;
$headings-color: null !default;

$display1-size: 3.74rem;
$display2-size: 3.145rem;
$display3-size: 2.89rem;
$display4-size: 2.63rem;

$display1-weight: 500;
$display2-weight: 400;
$display3-weight: 300;
$display4-weight: 300;
$display-line-height: $headings-line-height !default;

$lead-font-size: $font-size-base * 1.15;
$lead-font-weight: 300 !default;

$small-font-size: 80%;

$text-muted: $gray-700;

$blockquote-small-color: $gray-600 !default;
$blockquote-small-font-size: $small-font-size !default;
$blockquote-font-size: $font-size-base * 1.25 !default;

$hr-border-color: rgba($black, 0.1) !default;
$hr-border-width: $border-width !default;

$mark-padding: 0.1em;

$dt-font-weight: $font-weight-bold !default;

$kbd-box-shadow: inset 0 -0.1rem 0 rgba($black, 0.25);
$nested-kbd-font-weight: $font-weight-bold !default;

$list-inline-padding: 0.5rem !default;

$mark-bg: var(--search-term-highlight-bg);

$hr-margin-y: $spacer !default;
// Paragraphs
//
// Style p element.
p {
	line-height:1.6;
}
$paragraph-margin-bottom: 1.5rem;
$list-margin-bottom: $paragraph-margin-bottom !default;
// Tables
//
// Customizes the `.table` component with basic values, each used across all table variations.

$table-cell-padding: 0.75rem;
$table-cell-padding-sm: 0.4rem;

$table-color: $body-color;
$table-bg: null;
$table-accent-bg: rgba($slate, 0.05);
$table-hover-color: $table-color;
$table-hover-bg: rgba($slate, 0.075);
$table-active-bg: $table-hover-bg;

$table-border-width: $border-width;
$table-border-color: $border-color;

$table-head-bg: $gray-200;
$table-head-color: $text-color;

$table-dark-color: $white;
$table-dark-bg: $gray-600;
$table-dark-accent-bg: rgba($white, 0.05);
$table-dark-hover-color: $table-dark-color;
$table-dark-hover-bg: rgba($white, 0.075);
$table-dark-border-color: lighten($table-dark-bg, 7.5%);
$table-dark-color: $white;

$table-striped-order: odd;

$table-caption-color: $text-muted;

$table-bg-level: -9;
$table-border-level: -6;

// Buttons + Forms
//
// Shared variables that are reassigned to `$input-` and `$btn-` specific variables.

$input-btn-padding-y: 0.475rem; //0.375rem;
$input-btn-padding-x: 1.5rem;
$input-btn-font-family: null !default;
$input-btn-font-size: $font-size-base;
$input-btn-line-height: $line-height-base;

$input-btn-focus-width: 7px;
$input-btn-focus-color: $input-focus-border-color;
$input-btn-focus-box-shadow: 0 0 $input-btn-focus-width 1px $input-btn-focus-color;

$input-btn-padding-y-sm: 0.35rem;
$input-btn-padding-x-sm: 0.5rem;
$input-btn-font-size-sm: $font-size-sm;
$input-btn-line-height-sm: $line-height-sm;

$input-btn-padding-y-lg: 0.5rem !default;
$input-btn-padding-x-lg: 1rem !default;
$input-btn-font-size-lg: $font-size-lg !default;
$input-btn-line-height-lg: $line-height-lg !default;

$input-btn-border-width: $border-width;

// Buttons
//
// For each of Bootstrap's buttons, define text, background, and border color.
$fab-size: 54px;
$btn-padding-y: $input-btn-padding-y;
$btn-padding-x: $input-btn-padding-x;
$btn-font-family: $font-family-sans-serif;
$btn-font-size: $font-size-base;
$btn-line-height: 3rem;
$btn-white-space: nowrap;

$btn-padding-y-sm: $input-btn-padding-y-sm;
$btn-padding-x-sm: $input-btn-padding-x-sm;
$btn-font-size-sm: $input-btn-font-size-sm;
$btn-line-height-sm: $input-btn-line-height-sm;

$btn-padding-y-lg: $input-btn-padding-y-lg !default;
$btn-padding-x-lg: $input-btn-padding-x-lg !default;
$btn-font-size-lg: $input-btn-font-size-lg !default;
$btn-line-height-lg: $input-btn-line-height-lg !default;

$btn-border-width: $input-btn-border-width !default;

$btn-font-weight: $font-weight-normal;
$btn-box-shadow: none; //inset 0 0 0 99999px rgba(0,0,0,0.2), 0 0 0.5rem rgba(0, 0, 0, 0.3);
$btn-focus-width: 2px;
$btn-focus-box-shadow: $input-btn-focus-box-shadow;
$btn-disabled-opacity: .65;//0.2;
$btn-active-box-shadow: none;

$btn-link-disabled-color: $gray-500;

$btn-block-spacing-y: 0.5rem;

// Allows for customizing button radius independently from global border radius
$btn-border-radius: $rounded-pill;
$btn-border-radius-lg: $border-radius-lg;
$btn-border-radius-sm: $border-radius-sm;

$btn-transition: background 400ms;

// Forms

$label-margin-bottom: 0.5rem !default;

$input-padding-y: $input-btn-padding-y;
$input-padding-x: $input-btn-padding-x;
$input-font-family: $input-btn-font-family !default;
$input-font-size: $input-btn-font-size;
$input-font-weight: $font-weight-base !default;
$input-line-height: $input-btn-line-height !default;

$input-padding-y-sm: $input-btn-padding-y-sm !default;
$input-padding-x-sm: $input-btn-padding-x-sm !default;
$input-font-size-sm: $input-btn-font-size-sm !default;
$input-line-height-sm: $input-btn-line-height-sm !default;

$input-padding-y-lg: $input-btn-padding-y-lg !default;
$input-padding-x-lg: $input-btn-padding-x-lg !default;
$input-font-size-lg: $input-btn-font-size-lg !default;
$input-line-height-lg: $input-btn-line-height-lg !default;

$input-bg: $white;
$input-disabled-bg: $gray-300;

$input-color: color-yiq(theme-color("light"));
$input-border-color: $card-border-color;
$input-border-width: $input-btn-border-width;
$input-box-shadow: inset 0 1px 1px rgba($black, 0.075);

$input-border-radius: $border-radius;
$input-border-radius-lg: $border-radius-lg;
$input-border-radius-sm: $border-radius-sm;

$input-focus-bg: $input-bg;
$input-focus-border-color: lighten($acp_blue_lightest, 5%);
$input-focus-color: $input-color;
$input-focus-width: $input-btn-focus-width;
$input-focus-box-shadow: $input-btn-focus-box-shadow;

$input-placeholder-color: $gray-600;
$input-plaintext-color: $text-color;

$input-height-border: $input-border-width * 2 !default;

$input-height-inner: calc(
	#{$input-line-height * 1em} + #{$input-padding-y * 2}
) !default;
$input-height-inner-half: calc(
	#{$input-line-height * 0.5em} + #{$input-padding-y}
) !default;
$input-height-inner-quarter: calc(
	#{$input-line-height * 0.25em} + #{$input-padding-y / 2}
) !default;

$input-height: calc(
	#{$input-line-height * 1em} + #{$input-padding-y * 2} + #{$input-height-border}
) !default;
$input-height-sm: calc(
	#{$input-line-height-sm * 1em} + #{$input-btn-padding-y-sm * 2} + #{$input-height-border}
) !default;
$input-height-lg: calc(
	#{$input-line-height-lg * 1em} + #{$input-btn-padding-y-lg * 2} + #{$input-height-border}
) !default;

$input-transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out !default;

$form-text-margin-top: 0.25rem !default;

$form-check-input-gutter: 2.25rem;
$form-check-input-margin-y: 0rem;
$form-check-input-margin-x: 0.5rem;

$form-check-inline-margin-x: 0.75rem !default;
$form-check-inline-input-margin-x: 0.3125rem !default;

$form-grid-gutter-width: 10px !default;
$form-group-margin-bottom: 1rem !default;

$input-group-addon-color: $input-color !default;
$input-group-addon-bg: $gray-200 !default;
$input-group-addon-border-color: $input-border-color !default;

$custom-forms-transition: background-color 0.15s ease-in-out,
	border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out !default;

$custom-control-gutter: 0.5rem !default;
$custom-control-spacer-x: 1rem !default;

$custom-control-indicator-size: 2rem;
$custom-control-indicator-bg: $input-bg !default;

$custom-control-indicator-bg-size: 50% 50% !default;
$custom-control-indicator-box-shadow: $input-box-shadow !default;
$custom-control-indicator-border-color: $link-color;
$custom-control-indicator-border-width: $input-border-width !default;

$custom-control-indicator-disabled-bg: $input-disabled-bg !default;
$custom-control-label-disabled-color: $gray-600 !default;

$custom-control-indicator-checked-color: $component-active-color !default;
$custom-control-indicator-checked-bg: $component-active-bg !default;
//$custom-control-indicator-checked-disabled-bg: rgba(theme-color("primary"), 0.5) !default;
$custom-control-indicator-checked-disabled-bg: rgba($slate, 0.5);
$custom-control-indicator-checked-box-shadow: none !default;
$custom-control-indicator-checked-border-color: $custom-control-indicator-checked-bg !default;

$custom-control-indicator-focus-box-shadow: $input-focus-box-shadow;
$custom-control-indicator-focus-border-color: $input-focus-border-color;

$custom-control-indicator-active-color: $component-active-color !default;
$custom-control-indicator-active-bg: lighten(
	$component-active-bg,
	35%
) !default;
$custom-control-indicator-active-box-shadow: none !default;
$custom-control-indicator-active-border-color: $custom-control-indicator-active-bg !default;

$custom-checkbox-indicator-border-radius: $border-radius;
$custom-checkbox-indicator-icon-checked: str-replace(
	url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='#{$custom-control-indicator-checked-color}' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e"),
	"#",
	"%23"
) !default;

$custom-checkbox-indicator-indeterminate-bg: $component-active-bg !default;
$custom-checkbox-indicator-indeterminate-color: $custom-control-indicator-checked-color !default;
$custom-checkbox-indicator-icon-indeterminate: str-replace(
	url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 4 4'%3e%3cpath stroke='#{$custom-checkbox-indicator-indeterminate-color}' d='M0 2h4'/%3e%3c/svg%3e"),
	"#",
	"%23"
) !default;
$custom-checkbox-indicator-indeterminate-box-shadow: none !default;
$custom-checkbox-indicator-indeterminate-border-color: $custom-checkbox-indicator-indeterminate-bg !default;

$custom-radio-indicator-border-radius: 50%;
$custom-radio-indicator-icon-checked: str-replace(
	url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3e%3ccircle r='3' fill='#{$custom-control-indicator-checked-color}'/%3e%3c/svg%3e"),
	"#",
	"%23"
) !default;

$custom-switch-width: $custom-control-indicator-size * 2.5;
$custom-switch-indicator-border-radius: 5em; //$custom-control-indicator-size / 2 !default;
$custom-switch-indicator-size: calc(
	#{$custom-control-indicator-size} - #{$custom-control-indicator-border-width *
		4}
);

$custom-select-padding-y: $input-padding-y;
$custom-select-padding-x: $input-padding-x;
$custom-select-font-family: $input-font-family;
$custom-select-font-size: $input-font-size;
$custom-select-height: $input-height;
$custom-select-indicator-padding: 1rem; // Extra padding to account for the presence of the background-image based indicator
$custom-select-font-weight: $input-font-weight;
$custom-select-line-height: $input-line-height;
$custom-select-color: $input-color;
$custom-select-disabled-color: $gray-600;
$custom-select-bg: $input-bg;
$custom-select-disabled-bg: $gray-200;
$custom-select-bg-size: 8px 10px !default; // In pixels because image dimensions
$custom-select-indicator-color: $gray-800;
$custom-select-indicator: str-replace(
	url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 4 5'%3e%3cpath fill='#{$custom-select-indicator-color}' d='M2 0L0 2h4zm0 5L0 3h4z'/%3e%3c/svg%3e"),
	"#",
	"%23"
) !default;
$custom-select-background: $custom-select-indicator no-repeat right
	$custom-select-padding-x center / $custom-select-bg-size !default; // Used so we can have multiple background elements (e.g., arrow and feedback icon)

$custom-select-feedback-icon-padding-right: calc(
	(1em + #{2 * $custom-select-padding-y}) * 3 / 4 + #{$custom-select-padding-x +
		$custom-select-indicator-padding}
) !default;
$custom-select-feedback-icon-position: center right
	($custom-select-padding-x + $custom-select-indicator-padding) !default;
$custom-select-feedback-icon-size: $input-height-inner-half
	$input-height-inner-half !default;

