.svg-icon {
	display: inline-block;
	position: relative;
	//top: .125em;
	width: 1em;
	height: 1em;
	stroke-width: 0;
	stroke: currentColor;
	fill: currentColor;
	vertical-align: middle;
	transition: fill 0.3s;
	flex-shrink: 0;

	> use {
		// The <use> elements in the svg icons eat up the click events in icon buttons in Edge. This seems to solve that:
		pointer-events: none;
	}
}

