svg.defs-only {
	display: block;
	position: absolute;
	height: 0;
	width: 0;
	margin: 0;
	padding: 0;
	border: none;
	overflow: hidden;
}
.empty-svg-icon.svg-icon {
	fill: $gray-400 !important;
}
