.keypoint-title {
	font-weight: $font-weight-normal;
}
//topic outline
ul.keypoints-list {
	list-style-position: outside;
	padding-left: 1rem;
	list-style-type: square;
	margin-left:1.6rem;
	margin-bottom: 2rem;

	li {
		margin:1rem 1rem 1.6rem;
		padding-left:.75rem;
		&::marker {
			color:$orange;
		}
		&.hvc {
			list-style-type: none;
			&:before {
				content: "";
				width: 1.6rem;
				height: 1.6rem;
				margin-right: 1.5rem;
				margin-left: -3.2rem;
				display: inline-block;
				background: transparent url("../../images/hvc-icon-mono-org.svg")
					no-repeat center center;
				background-size: cover;
				vertical-align: -3px;
			}
		}
	}
}
//question critique

.critique .keypoints {
	padding: 1.5rem;
	border-top: 0.3rem solid $gray-300;
	border-bottom: 0.1rem solid $gray-300;
	margin-bottom: 1rem;
	background-color: var(--card-background, $white);
	width: 100%;
	margin-left: 0;
	h4 {
		margin-top: 0;
		margin-bottom: 0;
		font-size: 1.15rem;
		color: var(--card-color, $gray-600);
		font-weight: $font-weight-light;
	}
	p {
		margin-bottom: 0;
		margin-top: 0.25rem;
	}
	/*
	@include phone {
		float: right;
		width: 45%;
		margin: 0.5em 0 1em 2em;
	}*/
	&:after {
		display: table;
		clear: both;
		content: "";
	}
}
@include media-breakpoint-up(xl) {
	.critique .keypoints {
		width: 50%;
		margin-left: 1.6rem;
	}
}
//text
aside.keypoints {
	padding: 1.5rem;
	border-top: 0.3rem solid $gray-300;
	border-bottom: 0.1rem solid $gray-300;
	margin-bottom: 1rem;
	background-color: var(--card-background, $gray-200);
	margin: 1rem 0 2rem;
	line-height: $line-height-base;
	.title {
		margin-top: 0;
		//font-size: 1.15rem;
		color: var(--card-color, $gray-600);
		font-weight: $font-weight-light;
	}
	ul {
		list-style-position: outside;
		padding-left: 1.6rem;
		list-style-type: square;
		margin-left:1.6rem;
		margin-bottom: 2rem;
	}
	li {
		margin:1rem 1rem 1.6rem;
		padding-left:.75rem;
		&::marker {
			color:$orange;
		}
		&.hvc {
			list-style-type: none;
			&:before {
				content: "";
				width: 1.6rem;
				height: 1.6rem;
				margin-right: 1.5rem;
				margin-left: -3.2rem;
				display: inline-block;
				background: transparent url("../../images/hvc-icon-mono-org.svg")
					no-repeat center center;
				background-size: cover;
				vertical-align: -3px;
			}
		}
	}
	p {
		color: var(--card-color, $gray-700);
	}
}
