.boardbasic-theme {
	section {
		margin-bottom:4rem;
	}
	.topic-content-wrapper ol {
		list-style-position: outside;
		padding-left: 1.6rem;
		margin-left:2rem;
		margin-bottom:2rem;
		li {
			margin: 0.75rem auto;
			margin-left:3px;
			padding-left:.75rem;
		}
	}
	//same as .bullets
	ul {
		list-style-position: outside;
		padding-left: 1.6rem;
		list-style-type: disc;
		margin-left:1.6rem;
		margin-bottom: 2rem;
		li {
			margin:1rem 1rem 1.6rem;
			padding-left:.75rem;
		}
		&.tricked {
			color:$danger;

		}
	}

}
b {
	font-weight: bold;
}
.abbreviation {
	border-bottom:1px dotted $acp_brown_mid;
	display: inline-block;
	  line-height: $line-height-sm;
}
.tricked {
	color:$danger;
	font-weight: 500;
	li {
		color:$text-color;
		&::marker {
		color:$danger;
		}
	}
}
.bb-alert {
	text-shadow: none;
	display: inline;
	box-decoration-break: clone;
	white-space: pre-wrap;
	word-wrap: break-word;
	@include background-opacity($highlighter-orange, 0.18);
	@extend .highlight-background;
}
.test, .test_answer {
	border-left:5px solid $acp_blue_lightest;
	margin-left:.5rem;
	padding:1.5rem;
	background-color:var(--card-background);
}
.test {
	margin-bottom:0;
	border-top:$card-border-width solid $card-border-color;
	border-right:$card-border-width solid $card-border-color;
}
.test_answer {
	border-top:1px solid #ddd;
	margin-bottom:3rem;
	border-bottom:$card-border-width solid $card-border-color;
	border-right:$card-border-width solid $card-border-color;
}

//special list style fix
section#mk19_bb_pm_s2_3 ul.tricked:first-of-type li:first-child  {
	list-style:none;
	margin-left:-4rem;
}

