.timer__wrapper {
	display: flex;
	flex-flow: row wrap;
}

.timer {
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	color: mksap-color("acp_violet"); //#702785;
	//font-size: 0.85em;

	.timer__time {
		display: flex;
		flex-flow: row wrap;
		align-items: center;
		text-align: center;
		justify-content: center;
		//font-size: 1.4em;
		/*
		@include phone-small-max {
			margin: 0 1em;
		}*/
		.timer__heading {
			display: flex;
			flex-flow: row nowrap;
			align-items: center;
		}
		.svg-icon {
			margin-right: 0;
		}
		.timer__title {
			margin-right: 0.5em;
			white-space: nowrap;
		}
		.timer__counter {
			display: flex;
			flex-flow: row nowrap;
			align-items: center;
			justify-content: center;
			padding: 0.1em;
		}
	}

	.timer__actions {
		margin-left: 16px;
		background-color: mksap-color("acp_blue_light");
		border-radius: 5em;
		color: #fff;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: center;
		.btn-icon {
			display: flex;
			align-items: center;
			justify-content: center;
			.svg-icon {
				width: 12px;
				height: 12px;
				fill: #fff;
			}
		}
	}
}
.masthead .timer__wrapper {
	display: flex;
	justify-content: flex-start;
	text-align: left;
	margin-left:4rem;
}
@include media-breakpoint-up(md){
	.masthead .timer__wrapper {
	justify-content: flex-end;
	text-align: right;
	}
}
.question-content > .timer__wrapper {
	justify-content: center;
	margin: 0.5em auto;
	padding: 1rem;
	background-color: rgba($white, 0.5);
	border-radius: $nav-pills-border-radius;
}
.card .list-group-item {
	.timer__icon {
		display: none;
	}
}
.timer.timer--times-up {
	color: mksap-color("acp_red");
	border-color: mksap-color("acp_red");

	.timer__icon .tick {
		background-color: mksap-color("acp_red");
		transform: rotateZ(125deg);
	}
}
.timer.timer--pause {
	color: $orange;
	border-color: $orange;
	.tick {
		background-color: $orange;
	}
}
.timer__icon {
	position: relative;
	margin-right: 8px;
	.tick {
		background-color: mksap-color("acp_violet");
		border-radius: 100%;
		height: 6px;
		left: 46.5%;
		position: absolute;
		top: 34%;
		transform-origin: 50% 100%;
		width: 2px;
	}
}

.pause-screen {
	opacity: 0.25;
	filter: blur(3px);
	pointer-events: none;
	user-select:none;
}

@keyframes rotate {
	100% {
		transform: rotateZ(360deg);
	}
}
