@media print {
	*,
	*:before,
	*:after,
	p,
	div,
	blockquote,
	li {
		background: transparent;
		color: #000 !important;
		box-shadow: none !important;
		text-shadow: none !important;
	}
	html,
	body,
	#app,
	main,
	.main,
	footer {
		background-color: transparent !important;
		-ms-overflow-style: none !important;
		overflow: visible !important;
		height: auto !important;
		display: block !important;
		float: none !important;
	}
	body.nightMode {
		background-color: #fff !important;
		color: #000 !important;
		.mk-card {
			background-color: #fff !important;
			color: #000 !important;
		}
	}
	@page {
		margin: 1cm;
		height: auto !important;
		width: auto !important;
		display: block !important;
		max-height: 100% !important;
		overflow: visible !important;
	}
	.app-screen {
		width: 100%;
		//flex: 0 0 100%;
		display: block !important;
		max-width: 100%;
		float: none;
		margin: 0;
		padding: 0;
		font-size: 12pt;
		line-height: 1.4;
		background: #fff !important;
		color: #000;
		height: auto;
		div {
			box-decoration-break: clone;
		}
	}
	.mk-card,
	.mk-card > .books {
		display: block !important;
		overflow: visible;
		height: auto;
		float: none;
	}
	.books {
		display: block !important;
		overflow: visible;
		height: auto;
		float: none;
	}
	.app-screen:before {
		content: "\ Copyright © 2019 American College of Physicians. All Rights Reserved.";
		color: #777 !important;
		font-size: 8pt;
		padding-top: 16px;
		text-align: center !important;
	}
	/* Defining all page breaks */
	a {
		page-break-inside: avoid;
	}
	blockquote {
		page-break-inside: avoid;
	}
	h1,
	h2,
	h3,
	h4,
	h5,
	h6 {
		page-break-after: avoid;
		page-break-inside: avoid;
	}
	img {
		page-break-inside: avoid;
		page-break-after: avoid;
	}
	table,
	pre {
		page-break-inside: avoid;
	}
	#app {
		.no-print,
		.no-print *,
		.sticky.pagination,
		.app-nav-drawer,
		.mk-top-app-bar,
		.well,
		.well-secondary,
		.well-visible {
			display: none !important;
		}
		.hosp {
			margin-left: 0;
		}
		.mk-top-app-bar--fixed-adjust {
			padding-top: 0;
		}
		.app-screen,
		.related-questions-wrap,
		.svg-hospitalist {
			background-color: transparent !important;
		}
		.keypoints {
			background-color: transparent !important;
			border: 1px solid #000;
		}
		.critique .keypoints {
			padding: 1em;
		}
		.critique .actions {
			display: none;
		}
		.question-content article.question .q_mcq {
			border: 1px solid #ccc;
		}
		.mk-card.flashcard,
		.mk-card.book {
			border: 1px solid #000;
		}
		.flashcard__progress {
			display: none;
		}
		.answer-bubble {
			background: inherit !important;
		}
		article.question .letter {
			border: 1px solid #aaa !important;
		}
		option .s_a.w_a .answer-bubble {
			border: 1px solid #dc291e;
		}
		option .s_a.r_a .answer-bubble {
			border: 1px solid #2eb135;
		}
	}
	.mdc-dialog.mdc-dialog--fullscreen.mdc-dialog--open {
		visibility: visible;
		overflow: visible !important;
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		.mdc-dialog__surface:before {
			content: "\ Copyright © 2019 American College of Physicians. All Rights Reserved. 190 North Independence Mall West, Philadelphia, PA 19106-1572";
			color: #777 !important;
			font-size: 8pt;
			padding-top: 16px;
			text-align: center !important;
		}
		.mdc-dialog__surface {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			visibility: visible;
			overflow: visible !important;
			width: 100%;
			height: 100%;
			.header__close {
				display: none;
			}
			.mdc-dialog__body.mdc-dialog__body--scrollable {
				height: auto !important;
				width: auto !important;
				display: block !important;
				max-height: 100% !important;
				overflow: visible !important;
				.answer-bubble {
					background: inherit !important;
				}
				article.question .letter {
					border: 1px solid #aaa !important;
				}
				option .s_a.w_a .answer-bubble {
					border: 1px solid #dc291e;
				}
				option .s_a.r_a .answer-bubble {
					border: 1px solid #2eb135;
				}
			}
			.mdc-dialog__backdrop {
				opacity: 1 !important;
				background-color: #fff !important;
			}
			.jump-to-content {
				display: none;
			}
		}
	}
	//dashboard reports
	.hero-img {
		display: none;
	}
	.promo.books .mk-card.book {
		.heading {
			background: transparent;
			.title > .svg-icon {
				fill: #000;
			}
		}
	}
	.promo.books .mk-card.book.card-full {
		.heading {
			background: transparent;
			.title > .svg-icon {
				fill: #000;
			}
		}
		.book--content.full {
			flex-flow: row nowrap;
			flex: 1 1 350px;

			.charts {
				flex-flow: column nowrap;
				flex: 1 1 15%;
				margin: 0 1em;
				.donut-container,
				.pie-container {
					flex-flow: column nowrap;
					width: 100%;
					.mk-list {
						width: 100%;
					}
				}
				.donut-chart,
				.pie-chart {
					margin: 0;
					max-width: 130px;
				}
				.donut-chart:first-child {
					margin-bottom: 1em;
				}
			}
			.books-graphs {
				flex: 1 1 70%;
				margin: 0 1em;
				.vert__graph {
					.progress__bar {
						transition: none;
						&--correct,
						&--incorrect,
						&--unsure {
							opacity: 1;
							visibility: visible;
							animation: none;
						}
					}
				}
			}
		}
	}
	.progress__graph {
		.progress__bar--incorrect,
		.progress__bar--correct,
		.progress__bar--answered {
			opacity: 1 !important;
			visibility: visible !important;
			animation: none !important;
			transition: none !important;
		}
	}
	//no columns for print
	.note__menu {
		display: none;
	}
}
