//default accordion styles

.accordion {
	.card {
		overflow: hidden;
		border-radius: 0;
		//border:none;
		border-left:0;
		border-right:0;
		box-shadow: none;
		.card-header {
			margin-bottom: -$card-border-width;
			font-size: 1.7rem;
			padding: 1.25rem;
			display: flex;
			align-items: center;
			justify-content: flex-start;
			border: none;
			color: $text-color;
			.svg-icon {
				fill:var(--theme-primary);

			}
			&:not(:disabled):not(.disabled):hover,
			&:not(:disabled):not(.disabled):focus,
			&:not(:disabled):not(.disabled):active {
				background-color: $list-group-hover-bg;
				cursor: pointer;
				text-decoration: none;
				
			}
			&:focus {
				outline: 0;
				// TODO: This box shadow is geting hidden by the parent's overflow: hidden :shrug:
				box-shadow: $input-focus-box-shadow;
			  }
		}
		.list-group-item-action {
			cursor: pointer;
		}
	}
}

//topic outline and blueprint styles
.outline-expansion {
	.section{
		&:first-of-type {
			border-top-left-radius:0;
			border-top-right-radius:0;
		}
		&:last-of-type {
			.card {
				border-bottom-left-radius:$border-radius-lg;
				border-bottom-right-radius:$border-radius-lg;
			}
		}
		&.sect3 {
			border-radius: inherit;
		}
	}
	.expansion-card {
		.card {
			.card-body {
				background-color: mksap-color("float-bg");
				.card {
					border:$card-border-width solid $card-border-color;
					border-radius: $border-radius-lg;
					.bubble-list .list-group-item {
						border:none;
					}
				}
			}
		}

	}
}


.expansion-card {
	.title-wrapper {
		flex: 1;
		min-width: 0;
		width: 100px;
		font-size: 1.8rem;
		padding-left:1rem;
	}

}

//add accordion-wrapper to add border radious and bg-color of stacked expansion cards
.accordion-wrapper .expansion-card {
	.card {
		border-radius:0;
	}
	.open.card .card-body {
		background-color:var(--card-background);
	}
}
.accordion-wrapper .expansion-card:first-of-type{
	.card {
		border-top-left-radius: $border-radius-lg;
		border-top-right-radius: $border-radius-lg;
	}
}
.accordion-wrapper .expansion-card:last-of-type{
	.card {
		border-bottom-left-radius: $border-radius-lg;
		border-bottom-right-radius: $border-radius-lg;
	}
}

//related content accordion at bottom of chapters
.related-content-card .accordion.expansion-card {
	&:last-of-type{
		.card {
			border-bottom-left-radius: $border-radius-lg;
			border-bottom-right-radius: $border-radius-lg;
		}
	}
	&> .card {

		.card-header {
			font-weight: normal;
			@include font-size($font-size-base);
			.svg-icon {
				fill: mksap-color("acp_blue_dark");
			}
		}
		.card-body {
			padding:0;
		}
	}
}
.related-content-card {
	.accordion .open.card .card-header:not(:disabled):not(.disabled):hover,
	.accordion .open.card .card-header:not(:disabled):not(.disabled):active {
		color: var(--question-theme);
	}
}
//reference ranges nlv
.reference-results .accordion.expansion-card {
	.card {
		border:$card-border-width solid $card-border-color;
	}
}
