html:not(.native-app) .show-in-app {
	display: none;
}

.native-app {
	.hide-in-app {
		display: none !important;
	}
}

.native-app-login-form {
    height: 100vh;
    width: 100vw;
	background:radial-gradient(circle farthest-side, #184799, #003497);
    background-position:center;
    background-repeat: no-repeat;
    background-size: cover;

    .card {
        margin-top: 3rem;

        width:100%;
        max-width:600px;
    }

    img.mksap-acp-brand{
        width: 100%;
        margin-bottom: 3rem;
    }
}
