//error pages
.errors {
	display: flex;
	flex-flow: row nowrap;
	align-items: flex-start;
	justify-content: center;
	.image-wrapper {
		display: flex;
		flex-flow: column nowrap;
		align-items: flex-end;
		.image-container {
			display: flex;
			flex-flow: column nowrap;
			align-items: center;
			border: 1px solid $gray-300;
			img {
				margin: 1em;
				height: auto;
				max-width: 250px;
			}
			.caption {
				font-size: 0.75em;
				padding: 1em;
				display: flex;
				flex-flow: row nowrap;
				max-width: 250px;
			}
		}
		a.credit {
			font-size: 0.65em;
			align-self: flex-end;
			padding: 5px;
			text-decoration: none;
			color: $slate;
		}
	}
	.message-wrapper {
		font-size: 1em;
		margin: 1.5em 1em 0;
		text-align: left;
		h1 {
			font-size: 1.5em;
			color: $slate;
		}
	}
}
