// component used on answersheets - formerly known as a-matrix

@mixin acp-bubble-color-helper(
	$bubble-color,
	$bubble-bg,
	$hover-color: $bubble-color,
	$hover-bg: $bubble-bg
) {
	--bubble-color: #{$bubble-color};
	--bubble-bg: #{$bubble-bg};
	--bubble-active-color: #{$link-color};
	//--bubble-active-color: #{lighten($bubble-color, .15)};

	--button-hover-color: #{$hover-color};
	--button-hover-bg: #{$hover-bg};
}

.bubble-list {
	display: flex;
	flex-flow: column nowrap;
	align-items: flex-start;
	justify-content: flex-start;
	width: 100%;
	.list-group-item {
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-between;
		width: 100%;
		.eo {
			font-size:$font-size-base * 1.0625;
		}
		.btn-link {
			white-space: nowrap;
		}
		.response-item {
			min-width:6rem;
			display:flex;
			flex-flow: row wrap;
			align-items:center;
			justify-content:flex-start;
			& > .svg-icon {
				margin:.5rem;
			}
			.svg-star {
				fill:$acp_gold;
			}
		}
		@include media-breakpoint-down(sm) {
			.btn-link {
				align-self: flex-end;
				//margin-left:5px;
				margin-top:1rem;
			}

		}
	}
}

.bubble-grid {
	//animation-fill-mode: forwards;
	border-top: none;
	display: flex;
	flex-flow: row wrap;
	list-style-type: none;
	align-content: center;
	box-sizing: border-box;
	justify-content: space-around;
	.list-group-item {
		display: inline-flex;
		flex-flow: row wrap;
		border: none;
		.eo {
			display: none;
		}
		.btn-link {
			display: none;
		}
	}
}
.bubble {
	border: 0.1rem solid var(--bubble-color, $gray-400);
	border-radius: 50rem;
	background-color: var(--bubble-bg, $white);
	fill: var(--bubble-color);
	color: var(--bubble-color, $gray-600);
	text-decoration:none;
	@include font-size($font-size-sm);
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	justify-content: center;
	width: 11.8rem;
	height: 3.2rem;
	flex-shrink: 0;
	margin: 0.5rem;
	&--small {
		@extend .bubble;
		width: 7rem;
	}
	&__icon {
		display: flex;
		font-size: 1rem;
		margin-right: 0.35rem;
		align-items: center;
		.svg-icon {
			width: 1.2rem;
			height: 1.2rem;
			margin-left: 0.5rem;
			transition: none;
		}
	}
	&__product {
		margin-right: 0.35rem;
		@include font-size($font-size-sm);
	}
	&__no {
		font-weight: 600;
		@include font-size($font-size-sm);
	}

	&:disabled,
	&.disabled {
		background-color: $gray-300;
		color: $btn-link-disabled-color;
		fill: $btn-link-disabled-color;
		border: 1px solid $gray-300;
		pointer-events: none;
		&:active {
			background-color: var(--bubble-bg);
			color: var(--bubble-active-color);
			border: 1px solid var(--bubble-color);
		}
		&:hover {
			background-color: $gray-300;
			color: $btn-link-disabled-color;
			pointer-events: none;
		}
	}

	&:hover:not(.disabled),
	&:active:not(.disabled),
	&.active:not(.disabled) {
		background-color: var(--button-hover-bg, lighten($acp_blue_light, 50%));
		.bubble__product,
		.bubble__no {
			color: var(--button-hover-color);
		}
		svg {
			fill: var(--button-hover-color);
		}
	}

	&:hover {
		text-decoration: none;
		cursor: pointer;
	}
	&:focus {
		outline: none;
		box-shadow: 0px 0px 5px $input-focus-border-color;
	}
	&.unanswered {
		@include vary-with-night-mode {
			//background: darken(mksap-color-raw("gray-100"), 10%);
			@include acp-bubble-color-helper(
				$gray-600,
				$white,
				$white,
				mksap-color-raw("acp_blue_mid")
			);
		}
	}
	&.correct {
		@include vary-with-night-mode {
			@include acp-bubble-color-helper(
				mksap-color-raw("correct"),
				lighten(mksap-color-raw("correct"), 55%),
				#fff,
				mksap-color-raw("correct")
			);
		}
	}
	&.wrong {
		@include vary-with-night-mode {
			@include acp-bubble-color-helper(
				mksap-color-raw("wrong"),
				lighten(mksap-color-raw("wrong"), 43%),
				#fff,
				mksap-color-raw("wrong")
			);
		}
	}
}

// hide the bubble grid right and wrong answers during a test. revealed after test complete.
.exam-not-complete.bubble-grid {
	.bubble.correct,
	.bubble.wrong,
	.bubble.correct.starred,
	.bubble.wrong.starred {
		@include acp-bubble-color-helper(#fff, $gray-600);
		border: none;
		cursor: auto;

		.svg-icon.svg-remove,
		.svg-icon.svg-check {
			display: none !important;
		}
	}
}
//.bubble.correct:hover:hover svg
//to fix flexbox last row alignment issue
.bubble-grid__placeholder {
	visibility: hidden;
	height: 0;
	padding: 0;
	margin: 0.5rem;
	border: 0;
	box-shadow: none;
	width: 7.0rem;
}

// add to list style components or utility?
ul.reset {
	list-style-position: inside;
	list-style-type: none;
	margin: 0;
	padding: 0;
	text-indent: 0;
}
