
.search-results {
	.nav.nav-tabs {
		background-color: mksap-color("gray-200");
		border-top-right-radius: $border-radius-lg;
		border-top-left-radius: $border-radius-lg;
		.nav-link:active.text-text-theme .svg-icon,
		.nav-link.active.text-text-theme .svg-icon {
			color: mksap-color("text-theme");
			fill: mksap-color("text-theme");
		}
		.nav-link:active.text-question-theme .svg-icon,
		.nav-link.active.text-question-theme .svg-icon {
			color: mksap-color("question-theme");
			fill: mksap-color("question-theme");
		}
		.nav-link {
			color:var(--theme-primary);
		}
		.nav-link:hover {
			background-color: $nav-tabs-link-hover-bg;
		}
		.nav-link.active, .nav-tabs .nav-item.show .nav-link {
			border-color:#d0d0d0 #d0d0d0 transparent;
			color: $white;
			background-color: var(--theme-primary);
		}
		a[aria-expanded="false"] {
			background-color:$gray-400;

		}
	}
}
//hide the tab icons on small devices to make it fit
@media (max-width: 575.98px) {
	.search-results {
		padding:0;
	}
	.search-results .nav.nav-tabs .nav-link{
		font-size:90%;
	 	.svg-icon {
			display:none;
		}
	}
}



.search-result-banner {
	padding:2rem;
	color: $white;
}
.search-results {
	a.visited {
		color: $link-visited-color;
		h6 {
			color: $link-visited-color;
		}
	}
	.list-group-item:first-child {
		border-top: none;
	}
	.list-group-item {
		padding: 1rem 1.5rem;
		h6 {
			color: $blue;
			margin-bottom: 0;
			font-size: 1.5rem;
		}
		.subtitle {
			color: mksap-color("slate");
			display: block;
			font-style: italic;
		}
		&.promoted-child {
			padding-left: 5rem;
			font-size: 1.5rem;
			display: block;
			border: none;
			&:last-child {
				border-bottom-left-radius:$card-border-radius;
				border-bottom-right-radius:$card-border-radius;
			}
			&>span:before {
				content:"\2014   ";
				padding-right:1rem;
			}
			@include media-breakpoint-down(xs) {
				padding-left: 2rem;
			}
		}
		.snippet {
			font-size: 1.4rem;
			line-height: 1.5;
		}
	}

	.list-group-item.active {
		background-color: $list-group-hover-bg;
		color: $text-color;
	}
	@include media-breakpoint-down(xs) {
		.promoted-title {
			padding-top:3rem;
		}
	}
	.promoted .promoted-title::after {
		content: "Top Match";
		position: absolute;
		right: -18px;
		top: 0;
		background-color: rgba(0, 160, 223, 0.15);
		text-align: right;
		font-size: 1.2rem;
		padding: 1.5rem;
		background: (url(../../svg-icons/top-match-arrow.svg) no-repeat 10px 2px);
		width: 13rem;
		//line-height: 1.7rem;
	}
	.empty-message {
		text-align: left;
	}
}

//hightlighting search terms
mark {
	display: inline;
	background: mksap-color("search-term-highlight-bg");
	box-decoration-break: clone;
	white-space: pre-wrap;
	word-wrap: break-word;
	padding: 0 0.1rem;
	color: $text-color;
}
.search-highlight-toolbar {
	@include vary-with-night-mode {
		background: darken(desaturate(mksap-color-raw("acp_blue_lightest"), 22.67), 14.31);
	}
	color: $text-color;
	height: 54px;
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	justify-content: space-between;
	padding: 0 1.5em;
	box-sizing: border-box;
	flex: 1 1 auto;
	box-shadow: 0 -8px 6px -6px mksap-color("gray-200");
	@include only-in-night-mode {
		box-shadow: none;
	}
	@include fix-left-next-to-navbar;

	.main-toolbox-drawer-open & {
		right: $toolbox-right-drawer-width;
	}
	//@include sticky;
	&__content {
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-around;
		flex: 2 0 auto;
		width: 100%;
		.truncate {
			width: 90%;
		}
		.header__close .svg-icon.svg-close-xtra-thin {
			fill: $text-color;
		}
	}
}

.search-back-to-top {
	position: absolute;
	bottom: 20px;
	right: 30px;
	z-index: 99;
	background-color: #007e66;
	color: white;
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	justify-content: center;
	.svg-icon {
		fill: white;
	}
	&.search-back-to-top--hide {
		display: none;
	}
}
