// open tables and figures in a new window
//hacky styles to hide items that cause confusion

.chromeless {
	#app {
		position: relative;
	}
	.app-nav-drawer.app-nav-drawer {
		display: none;
	}
	header.top-app-bar {
		display: none;
	}
	.top-app-bar--fixed-adjust {
		padding: 0;
	}
	.app-screen {
		position: relative;
		left: auto;
		right: auto;
		top: auto;
		bottom: auto;
	}
	main {
		padding-top: 0;
		margin-top:0;
	}
	.header__close {
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: flex-end;
	}
}
