.landing {
	min-height: 150vh;
	max-width: 1100px;
	margin: 0 auto;
	border: 1px solid #ccc;
    -webkit-box-shadow: 0 0 5px #ccc;
    box-shadow: 0 0 5px #ccc;
	background: $body-bg;

}
.intro {
	background: #fff url('../../images/login-bg.png') no-repeat;
	background-size: 1003px 622px;
	padding: 3rem;
	.logo-wrapper {
		.mksap-branding {
			display: block;
			max-width: 300px;
			img {
				width: 300px;
			}
		}
		.tagline {
			text-align: left;
			margin: 0.5em auto;
			font-size: 0.8em;
			letter-spacing: 0.035em;
			color: $slate;
			white-space: nowrap;
		}
	}
	.intro-card {
		@include media-breakpoint-up(sm) {
			display: flex;
			flex-direction: row;
		}
		display: block;
		max-width: 600px;
		margin: 3rem auto;
		overflow: hidden;
		.signup,
		.login-form {
			flex: 1 1 0;
			align-items: center;
			justify-content: center;
			padding: 1.5em 2.5em 2em;
			h3 {
				margin-top: 0;
				margin-bottom: 0.8em;
				font-weight: 400;
				text-align: center;
			}
			label {
				display: block;
				font-size: 0.8em;
			}
			.error {
				padding: 1rem;
				margin-bottom: 1rem;
				border-radius: 0.9rem;
				svg {
					margin-right: .5rem;
				}
				span {
					font-size: 0.8em;
					line-height: 1.5;
				}
			}
		}
		.signup {
			background: $body-bg;
			text-align: center;
		}
		.login-form {
			.horz {
				margin-bottom: 0.75em;
				width: 100%;
				input {
					width: 100%;
					color: $text-color;
				}
			}
			.remember_me {
				display: flex;
				justify-content: flex-start;
				align-items: center;
				margin-bottom: 0.75em;
				input {
					margin-right: 0.5em;
				}
			}
			.btn a {
				width: 100%;
			}
			.forgot-uid-pass {
				display: block;
				padding: 0;
				margin: 0;
				margin-top: 1em;
				line-height: 1.25em;
				font-size: 0.8em;
			}
			.message.error {
				background-color: mksap-color("acp_red");
				color: #fff;
				line-height: 1rem;
				padding: 0.5em;
				i {
					float: left;
					height: 100%;
				}
				h3 {
					margin-top: 0;
					color: #fff;
				}
				label {
					color: #fff;
				}
				input {
					margin-bottom: 0.5em;
					color: $text-color;
				}
			}
			input:-webkit-autofill,
			input:-webkit-autofill:hover,
			input:-webkit-autofill:focus input:-webkit-autofill,
			textarea:-webkit-autofill,
			textarea:-webkit-autofill:hover textarea:-webkit-autofill:focus,
			select:-webkit-autofill,
			select:-webkit-autofill:hover,
			select:-webkit-autofill:focus {
				width: 100%;
				color: $text-color;
			}
		}
	}
}


.video-cards-container {
	margin-top: 3rem;
	text-align: center;
	.card-header {
		> .svg-icon {
			margin-right: 0.5rem;
		}
		.svg-grid {
			fill: $acp_blue_dark;
		}
		.svg-target {
			fill: $acp_blue_light;
		}
		.svg-books {
			fill: $acp_green;
		}
	}
	.card-body {
		padding: 1em;
		display: block;
		video {
			width: 100%;
		}
	}
	@include media-breakpoint-up(sm) {
		text-align: left;
		.card-body{
			display: flex;
		}
		.description-container,
		.video-container {
			flex: 1 1 0;
			padding-left: 1em;
		}
	}
}

.mini-cards-container {
	margin-top: 3rem;
	.card {
		margin-bottom: 3rem;
	}
	.large-icon img{
		width: 70px;
		margin: 1rem auto;
		color: $text-color;
	}
	h3 {
		margin-top: 2rem;
		font-weight: 300;
		font-size: 2rem;
	}
	.card-body {
		padding: 1em;
		text-align: center;
	}
	@include media-breakpoint-up(sm) {
		display: grid;
		grid-template-columns: 1fr 1fr;
		grid-gap: 3rem;
		.card {
			margin-bottom: 0;
		}
	}
}

.learn-more {
	margin-top: 3rem;
	padding-bottom: 2em;
	vertical-align: middle; //ie fallback
	background-color: $nav-bar-bg-color;
	color: #fff;
	text-align: center;
	&__brand {
		padding: 2em;
		img {
			width: 100%;
			max-width: 300px;
			margin: 0 auto;
		}
	}
	&__intro {
		font-weight: 300;
		font-size: 2rem;
		text-align: center;
		margin: 0 2em 1em;
		@include media-breakpoint-up(sm) {
			margin: 0 6em 1em;
		}
	}
	a.btn {
		display: inline-block;
		margin: 0 auto;
		padding: 0.5em 2em;
	}
}

.footer {
	vertical-align: middle; //ie fallback
	text-align: center;
	padding: 2em 0 3em;
	background: #fff;
	.lndng-bottom-login {
		text-align: center;
	}
	.acp-link {
		margin: 0 auto 2rem;
		img {
			margin-left: 0.5em;
			width: 100%;
			max-width: 300px;
		}
	}
	@include media-breakpoint-up(md) {
		-ms-grid-row: 7;
		-ms-grid-column: 2;
		-ms-grid-column-span: 5;
		grid-column: col 2 / span 5;
		grid-row: row 7;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-between;
		.acp-link, .lndng-bottom-login {
			margin: 0;
		}
		.lndng-bottom-login {
			display: flex;
			justify-content: center;
			width: 50%;
			margin: 1rem auto;
			p {
				margin: 0 10px 0 0;
				line-height: 2.5em;
			}
		}
		.acp-link {
			width: 50%;
		}
	}
}
