$root-font-size: 62.5%; // based on 10/16 = 62.5  base font size of 10px divided by browser default font size of 16px - use rem

@mixin font-size-variant($name, $multiple) {
	&.font-#{$name} {
		font-size: $root-font-size * $multiple;
	}
}
html {
	font-size: $root-font-size;
	//1.125-major second - https://type-scale.com/
	@include font-size-variant("up-1", 1.09);
	@include font-size-variant("up-2", 1.188);
	@include font-size-variant("up-3", 1.295);
	@include font-size-variant("down-1", 0.917);
	@include font-size-variant("down-2", 0.842);
	@include font-size-variant("down-3", 0.772);
}
.nobr {
	white-space: nowrap;
}

//don't include these in font sizing
html.font-up-3,
html.font-up-2,
html.font-up-1,
html.font-down-3,
html.font-down-2,
html.font-down-1 {
	.btn, .nav-link, .top-app-bar .btn-icon, .pagination-item {
		font-size:17px;
	}
	@include media-breakpoint-down(xs) {
		.svg-logo-acp-mksap19-w {
			font-size:24px;
		}
	}
}


