.rating-prompt {
	.prompt {
		padding: 0 0.5em;
		display: flex;
		flex-flow: row wrap;
		align-items: center;
		justify-content: flex-start;
		width: 100%;
		&-text {
			margin-right: 1em;
			margin-bottom: 1em;
		}
		&-action {
			margin-bottom: 1em;
		}
	}
}
