// on settings page and after feedback prompt
.ratings {
	&__form-wrapper {
		display: flex;
		flex-flow: column nowrap;
		align-items: center;
		padding-bottom: 2em;
		p {
			width: 80%;
			text-align: center;
			&.ratings__label {
				margin: 0;
			}
		}
	}
	&__button-wrapper {
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-around;
		margin: 0.5em 1em;
	}
	&__label {
		margin: 0 0em 2em;
		font-style: italic;
	}
	&__form {
		width: 60%;
		display: flex;
		flex-flow: column;
		align-items: flex-end;
		/*
		@include phone-max {
			width: 90%;
		}*/
		.btn {
			display: flex;
			align-self: flex-end;
			margin: 1em;
		}
	}
	&__response {
		font-style: italic;
	}
}
.rating-submitted {
	padding: 9px;
	font-size: 18px;
	.svg-icon {
		fill: #f1a403;
	}
}
