.browsehappy {
	position: absolute;
	z-index: 99999;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	height: 100%;
	width: 100%;
	background: transparent repeat;
	.message-window {
		display: block;
		padding: 2em;
		margin: 4em auto;
		width: 60%;
		max-width: 640px;
		min-width: 320px;
		text-align: center;

		line-height: 1.5em;
		border: 3px solid #fff;
		background-color: #f5f5f5;
		p {
			margin-top: 0;
			margin-bottom: 1em;
		}
		.button {
			display: inline-block;
			padding: 0.5em 1em;
			margin: 1em auto 0;
			&.dismiss-warning {
				background-color: $acp_magenta;
			}
		}
	}
}
