// Build the color properties for a badges, to be used by .badge-* classes
@function badge-colors($bg, $color: color-yiq($bg)) {
	@return ("badge-color": $color, "badge-background": $bg);
}

@mixin mksap-badge-variant(
	$color-name,
	$base-color: mksap-color-raw($color-name, $mode: "day"),
	$night-base-color: mksap-color-raw($color-name, $mode: "night"),
	$badge-colors: badge-colors($base-color),
	$night-badge-colors: badge-colors($night-base-color)
) {
	@include define-component-variant(
		"badge",
		$color-name,
		$badge-colors,
		$night-badge-colors
	);
}



// Build the color properties for an outline button, to be used by .btn-outline-* classes
@function outline-badge-colors(
	$color
) {
	@return (
		"badge-color": #{$color},
		"badge-background": #{badge-color-yiq($color)},
		"badge-border": #{$color}
	);
}

@mixin mksap-badge-outline-variant(
	$color-name,
	$base-color: mksap-color-raw($color-name, $mode: "day"),
	$night-base-color: mksap-color-raw($color-name, $mode: "night"),
	$badge-colors: outline-badge-colors($base-color),
	$night-badge-colors: outline-badge-colors($night-base-color)
) {
	@include define-component-variant(
		"badge-outline",
		$color-name,
		$badge-colors,
		$night-badge-colors
	);
}


.badge {
	color: var(--badge-color);
	background-color: var(--badge-background);
	line-height: 1.6;
}
.badge-default {
	border: 1px solid $gray-300;
	color: var(--badge-color); //var(--theme-primary);
	background-color: var(--badge-background);
	line-height: 1.6;
}

@each $color in $mksap-theme-color-names {
	.badge-#{$color} {
		@include mksap-badge-variant($color);
	}
	.badge-outline-#{$color} {
		@include mksap-badge-outline-variant($color);
		border-style: solid;
		border-width: 1px;
		.svg-icon {
			fill:var(--badge-color) !important;
		}
	}
}
.badge-submission-ready-to-claim {
	@include mksap-badge-variant("submission-ready-to-claim");
}
.date-badge, .mcq-badge, .subspecialty-badge, .topic-badge {
	background-color:$white;//$manilla;// rgba(253, 249, 206, .35);//#e8eff9;
	border:$card-border-width solid $card-border-color;
	//color:$text-muted;
	font-weight:300;
}
.topic-badge {
	max-width:380px;
	.svg-icon {
		margin-bottom:3px;
	}
}
