.breadcrumb {
	display: flex;
	align-items: center;
	margin-bottom: 0;
	border-radius: $border-radius-lg;
	padding: 0; //0.25rem;
	.svg-icon {
		margin-left: 0.5rem;
	}
	.breadcrumb-item {
		margin-bottom: 0;
	}
}

.breadcrumb-item a {
	color: var(--link-color, white);
	border-radius: $btn-border-radius-lg;
	&:not(:disabled):not(.disabled):focus-visible,
	&:not(:disabled):not(.disabled).focus-visible,
	&:not(:disabled):not(.disabled):visited:focus-visible,
	&:not(:disabled):not(.disabled):visited.focus-visible {
		outline:0;
		border-color:$input-focus-border-color;
		@include box-shadow($input-box-shadow, $input-focus-box-shadow);
	}
}
.breadcrumb-item a:hover,
.breadcrumb-item a:active {
	text-decoration: underline;
	cursor: pointer;
}
.app-text .breadcrumb,
.app-questions .breadcrumb {
	.breadcrumb-item + .breadcrumb-item {
		&::before {
			content: escape-svg($breadcrumb-divider);
		}
	}
}

.modal-body > .breadcrumb {
	margin-bottom: 2rem;
}

.resume.card {
	.breadcrumb-item + .breadcrumb-item {
		&::before {
			display: inline-block; // Suppress underlining of the separator in modern browsers
			padding-right: $breadcrumb-item-padding;
			color: $breadcrumb-divider-color;
			content: escape-svg($breadcrumb-divider-light);
		}
	}
}
.nightMode {
	.breadcrumb-item + .breadcrumb-item {
		&::before {
			content: escape-svg($breadcrumb-divider);
		}
	}
	.breadcrumb-item.active {
		color: $breadcrumb-active-color-light;
	}
}
.nightMode {
	.app-text .breadcrumb,
	.app-questions .breadcrumb {
		.breadcrumb-item + .breadcrumb-item {
			&::before {
				content: escape-svg($breadcrumb-divider-light);
			}
		}
	}
}
