.toolbox-tab-content {
	flex-grow: 1;
	flex-shrink: 1;
	flex-basis: 400px;
	overflow-y: auto;
}

/* ---------------------------------------------------
      tool box right panel
  ----------------------------------------------------- */
.toolbox-right-drawer {
	flex-direction: column;
	align-items: flex-end;

	position: fixed;
	top: $top-app-bar-height;
	bottom: 0;
	right: 0;
	//margin-right: 10px; //browser scrollbar width
	width: $toolbox-right-drawer-width;

	transition: transform 0.25s;
	transform: translateX($toolbox-right-drawer-width);
	visibility: hidden;

	padding-top: 1rem;
	overflow: hidden;
	background-color: var(--card-background, $white);

	&.toolbox-right-drawer-open {
		visibility: visible;
		transform: translateX(0);
	}
}

.toolbox-content {
	flex-grow: 1;
	display: flex;
	flex-direction: column;

	.toolbox-right-drawer & {
		width: $toolbox-right-drawer-width;
	}

	.nav-pills {
		.nav-item {
			width: 33%;
			&:nth-of-type(even) {
				border-left: 1px solid $border-color;
				border-right: 1px solid $border-color;
			}
		}
		.nav-link {
			font-size: 1.25rem;
			//line-height: 1.5rem;
			padding: 0.5rem;
			//height: 65px;
			color: $text-color;
			background-color: var(--card-background, $white);
			border-top: 1px solid $border-color;
			border-bottom: 1px solid $border-color;
			&:not(:disabled):not(.disabled):focus-visible,
			&:not(:disabled):not(.disabled).focus-visible,
			&:not(:disabled):not(.disabled):visited:focus-visible,
			&:not(:disabled):not(.disabled):visited.focus-visible {
				outline:0;
				border-radius:$card-border-radius;
				box-shadow: $input-btn-focus-box-shadow;
			}
			&:hover {
				background: rgba(0, 126, 102, 0.2);
			}
			&.flex-column .svg-icon {
				margin-right: 0;
			}
		}
	}
}

.toolbox-toggle {
	display: none;
	border-radius: $border-radius-lg;
	position: fixed;
	z-index: 1;
	right: 17px; //browser scrollbar width
	top: 120px;
	background-color: var(--card-background, $white);
	width:9rem;
	@media (min-width: 1800px) {
		left: $container-padding-lg;
	}
	.shadow-sm {
		border-radius: $border-radius-lg;
	}
	.nav-pills {
		.nav-item {
			min-width: 9rem;
			border-bottom:1px solid $border-color;
			&:first-child {
				//border-bottom: 1px solid $border-color;
				.nav-link:hover {
					border-top-left-radius: $border-radius-lg;
					border-top-right-radius: $border-radius-lg;
					border-bottom-left-radius: 0;
					border-bottom-right-radius: 0;
				}
			}
			&:last-child {
				border-bottom:none;
				//border-top: 1px solid $border-color;
				.nav-link:hover {
					border-top-left-radius: 0;
					border-top-right-radius: 0;
					border-bottom-left-radius: $border-radius-lg;
					border-bottom-right-radius: $border-radius-lg;
				}
			}
		}
		.nav-link {
			color: $text-color;
			font-size: 1.4rem;
			padding: 0.5rem 0.75rem;
			&:not(:disabled):not(.disabled):focus-visible,
			&:not(:disabled):not(.disabled).focus-visible,
			&:not(:disabled):not(.disabled):visited:focus-visible,
			&:not(:disabled):not(.disabled):visited.focus-visible {
				outline:0;
				border-radius:$card-border-radius;
				box-shadow: $input-btn-focus-box-shadow;
			}
			&.active,
			&:active {
				color: white;
				.svg-icon {
					fill: var(--theme-primary);
				}
			}
			&.flex-column .svg-icon {
				margin-right: 0;
			}
		}
	}
}


.toolbox-toggle,
.toolbox-content {
	.nav-pills {
		.flex-column .svg-icon {
			fill: var(--theme-primary);
		}
		.nav-link:hover,
		.nav-link:active,
		.nav-link.active {
			background-color: var(--theme-primary);
			color: var(--theme-primary-text);//$white;
			.svg-icon {
				fill: $white;
			}
		}
	}
}

.toolbox-content {
	.accordion {
		.list-group-flush {
			.list-group-item {
				font-size: 1.4rem;
				background-color: mksap-color-alpha("acp_green", 0.07);
				padding-left: 2.75rem;
				&.active {
					color: $text-color;
					background: mksap-color-alpha("acp_green", 0.2);
					border-color: mksap-color("acp_green");
				}
			}

		}
	}
}


@include media-breakpoint-up(lg) {
	main.main-toolbox-drawer-open {
		margin-right: $toolbox-right-drawer-width;
		.app-questions,
		.app-text {
			//max-width: 100%;
			flex: 0 0 100%;
		}
	}
	.toolbox-right-drawer {
		display: flex;
	}

	.toolbox-toggle {
		display: block;
	}
}
@media (min-width: 1800px) {
	main, main.main-toolbox-drawer-open {
		margin-right: 0;
		.app-questions,
		.app-text {
			//max-width: 100%;
			flex: 0 0 100%;
		}
	}
}
.modal .toolbox-content .nav {
	border-left: 1px solid $border-color;
	border-right: 1px solid $border-color;
}
//board-basic theme toolbox
.toolbox-content.boardbasic-theme {
	.tab-pane{
		.list-group-flush {
			.list-group-item {
				display:flex;
				flex-flow:row nowrap;
				align-items:center;
				color: $text-color;
				.svg-icon {
					fill: var(--theme-primary);
				}
				&:hover,
				&:active,
				&.active {
					background-color: mksap-color-alpha("acp_brown_mid", 0.3);
					cursor: pointer;
					text-decoration: none;
				}
			}
		}
	}
}
