.print-answer-sheets {
    $answered: darkslategray;
	&.app-screen {
		left:0 !important;
	}
	.bubble.unanswered.bubble--xs{
		width:4rem;
		min-width:4rem;
		height:2.5rem;
		background-color:white;
		color:$answered;
	}

	.bubble.answered.bubble--xs{
		width:4rem;
		min-width:4rem;
		height:2.5rem;
		background-color:$answered;
		color:white;
	}
	.bubble-wrapper {
		display:flex;
		flex-flow:row nowrap;
		align-items: flex-start;
		justify-content:center;
	}
	.bubble-container {
		display:flex;
		flex-flow:column nowrap;
		align-items: flex-start;
		justify-content:flex-start;
			
	}

	.bubble-row {
		display:flex;
		flex-flow: row nowrap;
		align-items:center;
		justify-content:center;
		&__number {
			font-weight:bold;
			margin-right:3px;
		}
	}
	.grid-item.card {
		padding:0 5px;
		box-shadow:none;
		background-color: transparent;
		border:none;
	}


}
