//from mksap17 _answersheet.scss
$default-delay: 0.5s;
// Animation Mixin
@mixin bar-animation($time, $dimension) {
	visibility: hidden;
	@if $dimension == width {
		width: 0;
		animation: animate-width;
	}
	@if $dimension == height {
		height: 0;
		animation: animate-height;
	}
	animation-timing-function: cubic-bezier(0.35, 0.95, 0.67, 0.99);
	animation-duration: $time * 2;
	animation-fill-mode: forwards;
}

@keyframes animate-width {
	0% {
		width: 0;
	}
	100% {
		visibility: visible;
	}
}
@keyframes animate-height {
	0% {
		height: 0;
	}
	100% {
		visibility: visible;
	}
}

//standalone graph
.progress__graph {
	transition: all 0.25s;
	background: none;
	display: flex;
	justify-content: flex-start;
	width: 100%;
	overflow: hidden;
	height: 1.2rem;
	background-color: mksap-color("input-bg");//mksap-color("gray-light");
	transition: height 0.25s;
	margin: 5px auto;
	.incorrect,
	.unsure,
	.correct,
	.answered,
	.unanswered {
		opacity: 0;
		width: 0;
		flex: none;
	}
	&.fc {
		position: relative;
		.progress__bar {
			display: flex;
			justify-content: flex-start;
			width: 100%;
			position: absolute;
			left: 0;
			top: 0;
			bottom: 0;
			box-shadow: inset 0 0 3px #ababab;
			.progress__bar--correct {
				background-color: mksap-color("correct");
				//@include bar-animation($default-delay, width);
			}
			.progress__bar--unsure {
				background-color: mksap-color("unsure");
				border-radius: 0 0.2em 0.2em 0;
				//@include bar-animation($default-delay, width);
			}
			.progress__bar--incorrect {
				background-color: mksap-color("wrong");
				//@include bar-animation($default-delay, width);
			}
			.progress__bar--answered {
				background-color: $slate;
			}
		}
	}
	&.mcq {
		position: relative;
		.progress__bar {
			display: flex;
			justify-content: flex-start;
			width: 100%;
			position: absolute;
			left: 0;
			top: 0;
			bottom: 0;
			//z-index: 2;
			box-shadow: inset 0 0 3px #ababab;

			.progress__bar--correct {
				background-color: mksap-color("correct");
				//@include bar-animation($default-delay, width);
			}
			.progress__bar--incorrect {
				background-color: mksap-color("wrong");
				border-radius: 0 0.2em 0.2em 0;
				//@include bar-animation($default-delay, width);
			}
		}
		.progress__bar--answered {
			background-color: transparent;
			position: absolute;
			left: 0;
			top: 0;
			bottom: 0;
			//z-index: 1;
		}
	}
	&.cme {
		position: relative;
		.progress__bar {
			display: flex;
			justify-content: flex-start;
			width: 100%;
			position: absolute;
			left: 0;
			top: 0;
			bottom: 0;
			//z-index: 2;
			box-shadow: inset 0 0 3px #ababab;

			.progress__bar--claimed {
				background-color: mksap-color("submission-claimed");
				//@include bar-animation($default-delay, width);
			}
			.progress__bar--ready-to-claim {
				background-color: mksap-color("submission-ready-to-claim");
				border-radius: 0 0.2em 0.2em 0;
				//@include bar-animation($default-delay, width);
			}
			.progress__bar--not-earned {
				background-color: mksap-color("submission-not-earned");
				//@include bar-animation($default-delay, width);
			}
		}
	}
	&.exam {
		position: relative;
		.progress__bar {
			display: flex;
			justify-content: flex-start;
			width: 100%;
			position: absolute;
			left: 0;
			top: 0;
			bottom: 0;
			box-shadow: inset 0 0 3px #ababab;
		}
		.progress__bar--answered {
			background-color: lighten($slate, 10%);
			position: absolute;
			left: 0;
			top: 0;
			bottom: 0;
			//z-index: 2;
			//@include bar-animation($default-delay, width);
		}
	}
}
.progress {
	&__label {
		font-size: 1.5rem;
		font-weight: 300;
		text-align: right;
		color: var(--text-muted, $gray-700);
		white-space: nowrap;
		text-align: left;
		.correct {
			color: mksap-color("correct");
		}
		.complete {
			color: $slate;
		}
	}
}

//progress bar in a subspecialty book tile card
.books {
	.mk-card.book {
		.mk-card__primary-action {
			.progress__graph.mcq,
			.progress__graph.cme,
			.progress__graph.exam {
				background-color: transparent;
				box-shadow: none;
				height: 12px;
				margin-top: 0;
				background-color: mksap-color("input-bg");
				border-radius: 0;
				.progress__bar {
					.progress__bar--correct {
						border-radius: 0;
					}
				}
			}
		}
	}
}

//dashbaord subspecialty graphs
.graph-wrapper {
	@include media-breakpoint-up (md) {
		.rotate {
			.title, .progress__graph {
				transform:rotate(-90deg);
				width:200px;
			}
			.title {
				transform-origin:10% 100%;
				white-space:nowrap;
				overflow:hidden;
				text-overflow:ellipsis;
				position:absolute;
				left:20px;
				right:200px;
				bottom:20px;
			}
			.progress__graph {
				transform-origin:10% -50%;
			}
		}
	}
	&:before {
		content: '' ;
		display:block;
		height:200px;
		@include media-breakpoint-down (sm) {
			height:0;
		}
	}
}

///text section progress graph
.progress__graph.outline {
	background-color: transparent;
	height: auto;
	.svg-graduation-hat {
		fill: mksap-color("learningplan-theme");
	}
}
